@use 'variables' as v;
@use 'mixins' as m;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: v.$font-primary;
    background: var(--dark-bg);
    color: var(--text-primary);
    overflow-x: hidden;
    line-height: 1.6;
}

@include m.mobile {
    .hero h1 {
        font-size: 3rem;
    }

    .hero-buttons {
        flex-direction: column;
        align-items: center;
    }

    .features-grid {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .feature-card {
        padding: v.$spacing-md;
    }
}

.bg-animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -10;
    background: var(--gradient-dark);
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 20% 50%, rgba(0, 102, 255, 0.1) 0%, transparent 50%),
                    radial-gradient(circle at 80% 20%, rgba(0, 212, 255, 0.1) 0%, transparent 50%),
                    radial-gradient(circle at 40% 80%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
        animation: backgroundPulse 20s ease-in-out infinite;
    }
}

@keyframes backgroundPulse {
    0%, 100% { opacity: 0.5; transform: scale(1); }
    50% { opacity: 1; transform: scale(1.1); }
}

@keyframes float {
    0%, 100% { transform: translateY(0px) rotate(0deg); }
    33% { transform: translateY(-20px) rotate(120deg); }
    66% { transform: translateY(10px) rotate(240deg); }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in {
    @include m.fade-in-animation();
}

.section {
    padding: v.$spacing-xl v.$spacing-md;
    max-width: v.$desktop;
    margin: 0 auto;
}

.section-title {
    font-size: clamp(2.5rem, 5vw, 4rem);
    font-weight: 900;
    text-align: center;
    margin-bottom: v.$spacing-lg;
    padding-top: 12rem;
    @include m.gradient-text(var(--gradient-primary));

    &.regularPadding{
        padding-top: 0;
    }
}

.section-subtitle {
    text-align: center;
    font-size: 1.3rem;
    color: var(--text-secondary);
    margin-bottom: v.$spacing-lg;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.glow-cursor {
    position: absolute;
    width: 300px;
    height: 300px;
    background: radial-gradient(circle, rgba(0, 102, 255, 0.1) 0%, transparent 70%);
    border-radius: 50%;
    pointer-events: none;
    transition: all 0.1s ease;
    z-index: -1;
}

.btn-primary {
    background: var(--gradient-primary);
    color: white;
    padding: 1rem 2.5rem;
    border-radius: v.$radius-pill;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    border: none;
    cursor: pointer;
    display: inline-block;
    @include m.button-hover();
}

.btn-secondary {
    background: transparent;
    color: var(--text-primary);
    padding: 1rem 2.5rem;
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: v.$radius-pill;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    transition: all v.$transition-normal;
    backdrop-filter: blur(10px);
    
    &:hover {
        background: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.4);
        transform: translateY(-3px);
    }
}

.card {
    @include m.glassmorphism();
    border-radius: v.$radius-lg;
    padding: v.$spacing-md;
    @include m.card-hover();
}

.grid-2 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: v.$spacing-md;
}

.grid-3 {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: v.$spacing-md;
}

@include m.mobile {
    .section {
        padding: v.$spacing-lg v.$spacing-sm;
    }
    
    .btn-primary,
    .btn-secondary {
        width: 100%;
        max-width: 300px;
        text-align: center;
    }
    
    .grid-2,
    .grid-3 {
        grid-template-columns: 1fr;
    }
    
    // PASO 7: DiseÃ±o responsive mÃ³vil consistente para blog editor
    // MANTENER estructura de escritorio escalada, NO cambiar a columnas verticales
    
    .visual-editor {
        // Escalar todo el contenido del editor proporcionalmente
        transform: scale(0.8);
        transform-origin: top left;
        width: 125%; // Compensar el scale para que ocupe el ancho completo
        margin-left: 0;
        margin-right: 0;
    }
    
    .post-content {
        // Post content escalado igual que editor
        transform: scale(0.85);
        transform-origin: top left;
        width: 117.6%; // Compensar el scale (100/0.85)
        margin-left: 0;
        margin-right: 0;
    }
    
    // Filas de contenido - MANTENER HORIZONTAL (crucial para el PASO 7)
    .content-row {
        display: flex !important; // Mantener flex horizontal
        flex-direction: row !important; // NO cambiar a column
        gap: v.$spacing-xs; // Gap mÃ¡s pequeÃ±o pero manteniendo estructura
        margin: v.$spacing-sm 0;
        
        .content-column {
            flex: 1;
            min-width: 0;
            // No forzar width 100%, mantener flex para columnas
        }
    }
    
    // Bloques de contenido - mantener tamaÃ±os EXACTOS de escritorio
    .content-block {
        &.block-size-small {
            width: 50% !important; // Exactamente mismo porcentaje
        }
        
        &.block-size-medium {
            width: 80% !important; // Exactamente mismo porcentaje  
        }
        
        &.block-size-large {
            width: 95% !important; // Exactamente mismo porcentaje
        }
        
        // Alineaciones exactas
        &.block-align-left {
            margin-left: 0;
            margin-right: auto;
        }
        
        &.block-align-center {
            margin-left: auto;
            margin-right: auto;
        }
        
        &.block-align-right {
            margin-left: auto;
            margin-right: 0;
        }
    }
    
    // Toolbar lateral - escalar tambiÃ©n
    .editor-sidebar-toolbar {
        transform: scale(0.8);
        transform-origin: top left;
        width: 125%; // Compensar scale
        min-width: 125%;
        position: sticky;
        top: v.$spacing-sm;
    }
    
    // Videos mÃ¡s pequeÃ±os pero proporcionales
    .content-video iframe {
        height: 200px;
    }
    
    // Textos legibles pero estructura mantenida
    .text-content {
        font-size: 0.95rem;
        line-height: 1.5;
        
        span {
            &.font-small { font-size: 0.8rem !important; }
            &.font-normal { font-size: 0.95rem !important; }
            &.font-large { font-size: 1.1rem !important; }
            &.font-xl { font-size: 1.3rem !important; }
        }
    }
}

// Para pantallas muy pequeÃ±as - aÃºn mÃ¡s escalado pero manteniendo estructura
@media (max-width: 400px) {
    .visual-editor {
        transform: scale(0.7);
        width: 142.8%; // 100/0.7
    }
    
    .post-content {
        transform: scale(0.75);
        width: 133.3%; // 100/0.75
    }
    
    .editor-sidebar-toolbar {
        transform: scale(0.7);
        width: 142.8%;
    }
}