@use '../base/variables' as v;
@use '../base/mixins' as m;

.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 1rem 2rem;
    background: rgba(10, 10, 15, 0.9);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    z-index: 1000;
    transition: all 0.3s ease;

    &.scrolled {
        background: rgba(10, 10, 15, 0.95);
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    }
}

.nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
}

.logo {
    width: 5rem;
    height: auto;
    z-index: 1001;
    position: relative;
}

.nav-links {
    display: flex;
    list-style: none;
    gap: v.$spacing-md;
    align-items: center;
    margin: 0;
    padding: 0;

    a {
        color: var(--text-secondary);
        text-decoration: none;
        font-weight: 500;
        transition: all v.$transition-normal;
        position: relative;
        padding: v.$spacing-xs v.$spacing-sm;

        &:hover {
            color: var(--text-primary);

            &::after {
                width: 80%;
            }
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            width: 0;
            height: 3px;
            background: var(--gradient-primary);
            transition: all v.$transition-normal;
            transform: translateX(-50%);
        }
    }

    li:last-child a {
        &::after {
            display: none;
        }
        &:hover {
            color: var(--text-secondary);
        }
    }
}

.cta-button {
    background-color: var(--primary-color) !important;
    color: white !important;
    padding: 0.75rem 1.5rem;
    border-radius: 50px;
    text-decoration: none;
    font-weight: 600;
    position: relative;
    overflow: hidden;
    transition: background-color 0.5s ease-in-out, box-shadow 0.3s ease;

    &:hover {
        background-color: var(--accent-color) !important;
        box-shadow: 0 10px 10px rgba(69, 82, 102, 0.3);
    }
}

.mobile-menu-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
    padding: v.$spacing-xs;
    z-index: 1001;
    position: relative;
    width: 30px;
    justify-content: center;
    
    span {
        width: 25px;
        height: 3px;
        background: var(--text-primary);
        margin: 2px 0;
        transition: all 0.3s ease;
        border-radius: 2px;
        
        &:nth-child(1) {
            transform-origin: top left;
        }
        
        &:nth-child(2) {
            transform-origin: center;
        }
        
        &:nth-child(3) {
            transform-origin: bottom left;
        }
    }
    
    &.active {
        span {
            &:nth-child(1) {
                transform: rotate(45deg) translate(2px, 2px);
            }
            
            &:nth-child(2) {
                opacity: 0;
                transform: scaleX(0);
            }
            
            &:nth-child(3) {
                transform: rotate(-45deg) translate(2px, -2px);
            }
        }
    }
}

.mobile-nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(10, 10, 15, 0.95);
    backdrop-filter: blur(20px);
    z-index: 999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    
    &.active {
        opacity: 1;
        visibility: visible;
    }
    
    .mobile-nav-links {
        list-style: none;
        padding: 0;
        margin: 0;
        text-align: center;
        
        li {
            margin: 1.5rem 0;
            opacity: 0;
            transform: translateY(20px);
            transition: all 0.3s ease;
            
            a {
                color: var(--text-primary);
                text-decoration: none;
                font-size: 1.5rem;
                font-weight: 600;
                padding: 1rem 2rem;
                display: block;
                border-radius: 10px;
                transition: all v.$transition-normal;
                
                &:hover {
                    background: rgba(255, 255, 255, 0.1);
                    color: var(--primary-color);
                    transform: scale(1.05);
                }
                
                &.cta-button {
                    background: var(--gradient-primary) !important;
                    color: white !important;
                    margin-top: 1rem;
                    border-radius: 50px;
                    
                    &:hover {
                        background: var(--gradient-accent) !important;
                        transform: scale(1.1);
                    }
                }
            }
        }
    }
    
    &.active .mobile-nav-links li {
        opacity: 1;
        transform: translateY(0);
        
        @for $i from 1 through 6 {
            &:nth-child(#{$i}) {
                transition-delay: #{$i * 0.1}s;
            }
        }
    }
}

@media (max-width: 767px) {
    .navbar .nav-links {
        display: none !important;
    }

    .navbar .mobile-menu-toggle {
        display: flex !important;
    }
    
    .navbar {
        padding: 1rem !important;
    }
    
    .navbar .logo {
        width: 4rem !important;
    }
}

@include m.tablet {
    .nav-links {
        gap: v.$spacing-sm;
        
        a {
            font-size: 0.9rem;
        }
    }
}

@keyframes fadeInScale {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}