@use 'base/variables' as v;
@use 'base/mixins' as m;

.problem-hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: linear-gradient(135deg, #0a0a0f, #1a1a2e, #2d1b69);
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: 
            radial-gradient(circle at 20% 30%, rgba(255, 71, 87, 0.1) 0%, transparent 50%),
            radial-gradient(circle at 80% 70%, rgba(255, 170, 0, 0.1) 0%, transparent 50%),
            radial-gradient(circle at 40% 80%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
        animation: problemPulse 4s ease-in-out infinite alternate;
    }
}

@keyframes problemPulse {
    0% { opacity: 0.3; transform: scale(1); }
    100% { opacity: 0.6; transform: scale(1.1); }
}

.pain-indicator {
    display: inline-block;
    padding: v.$spacing-sm v.$spacing-md;
    background: rgba(255, 71, 87, 0.1);
    border: 1px solid rgba(255, 71, 87, 0.3);
    border-radius: v.$radius-pill;
    color: var(--danger-color);
    font-weight: 600;
    margin-bottom: v.$spacing-md;
    margin-top: v.$spacing-xl;
    @include m.pulse-animation(rgba(255, 71, 87, 0.2));
}

.problem-text {
    color: var(--danger-color);
    background: linear-gradient(135deg, #ff4757, #ff6b35);
    @include m.gradient-text(linear-gradient(135deg, #ff4757, #ff6b35));
}

.solution-text {
    @include m.gradient-text(var(--gradient-success));
}

.comparison-section {
    padding: v.$spacing-xl v.$spacing-md;
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
}

.comparison-container {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    gap: v.$spacing-lg;
    align-items: center;
    margin-top: v.$spacing-lg;
}

.before-card, .after-card {
    @include m.glassmorphism();
    border-radius: 25px;
    padding: v.$spacing-lg;
    position: relative;
    overflow: hidden;
    @include m.card-hover(1.02);
}

.before-card {
    border: 2px solid rgba(255, 71, 87, 0.3);

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at center, rgba(255, 71, 87, 0.1) 0%, transparent 70%);
        animation: problemGlow 3s ease-in-out infinite alternate;
    }
}

.after-card {
    border: 2px solid rgba(0, 255, 136, 0.3);

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at center, rgba(0, 255, 136, 0.1) 0%, transparent 70%);
        animation: solutionGlow 3s ease-in-out infinite alternate;
    }
}

@keyframes problemGlow {
    0% { opacity: 0.3; }
    100% { opacity: 0.6; }
}

@keyframes solutionGlow {
    0% { opacity: 0.3; }
    100% { opacity: 0.6; }
}

.card-header {
    display: flex;
    align-items: center;
    margin-bottom: v.$spacing-md;
    position: relative;
    z-index: 2;
}

.card-icon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.before-card .card-icon {
    background: linear-gradient(135deg, #ff4757, #ff6b35);
    animation: shake 2s ease-in-out infinite;
}

.after-card .card-icon {
    background: var(--gradient-success);
    animation: celebrate 2s ease-in-out infinite;
}

@keyframes shake {
    0%, 100% { transform: translateX(0); }
    25% { transform: translateX(-2px); }
    75% { transform: translateX(2px); }
}

@keyframes celebrate {
    0%, 100% { transform: scale(1) rotate(0deg); }
    50% { transform: scale(1.1) rotate(5deg); }
}

.card-title {
    font-size: 1.8rem;
    font-weight: 700;
    position: relative;
    z-index: 2;
}

.before-card .card-title {
    color: var(--danger-color);
}

.after-card .card-title {
    color: var(--success-color);
}

.problem-list, .solution-list {
    list-style: none;
    position: relative;
    z-index: 2;

    li {
        margin-bottom: 1.5rem;
        padding-left: v.$spacing-md;
        position: relative;
        color: var(--text-secondary);
        font-size: 1.1rem;
        line-height: 1.6;
    }
}

.problem-list li::before {
    content: 'âŒ';
    position: absolute;
    left: 0;
    top: 0;
    animation: problemShake 3s ease-in-out infinite;
}

.solution-list li::before {
    content: 'âœ…';
    position: absolute;
    left: 0;
    top: 0;
    animation: solutionPop 3s ease-in-out infinite;
}

@keyframes problemShake {
    0%, 100% { transform: rotate(0deg); }
    25% { transform: rotate(-5deg); }
    75% { transform: rotate(5deg); }
}

@keyframes solutionPop {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.2); }
}

.vs-divider {
    font-size: 4rem;
    font-weight: 900;
    @include m.gradient-text(var(--gradient-primary));
    animation: vsRotate 4s linear infinite;
}

@keyframes vsRotate {
    0% { transform: rotate(0deg) scale(1); }
    50% { transform: rotate(180deg) scale(1.2); }
    100% { transform: rotate(360deg) scale(1); }
}

.transformation-demo {
    padding: v.$spacing-xl v.$spacing-md;
    background: var(--gradient-dark);
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="circuit" width="20" height="20" patternUnits="userSpaceOnUse"><path d="M10,5 L10,15 M5,10 L15,10 M10,10 m-3,0 a3,3 0 1,0 6,0 a3,3 0 1,0 -6,0" fill="none" stroke="%23ffffff" stroke-width="0.5" opacity="0.05"/></pattern></defs><rect width="100" height="100" fill="url(%23circuit)"/></svg>');
        animation: circuitFlow 10s linear infinite;
    }
}

@keyframes circuitFlow {
    0% { transform: translateX(0) translateY(0); }
    100% { transform: translateX(-20px) translateY(-20px); }
}

.demo-container {
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

.chat-simulation {
    max-width: 600px;
    margin: 0 auto;
    @include m.glassmorphism();
    border-radius: 25px;
    padding: v.$spacing-md;
    position: relative;
    overflow: hidden;
    @include m.rotating-border(rgba(0, 102, 255, 0.5));

    &::before {
        animation: borderGlow 3s ease-in-out infinite alternate;
    }
}

@keyframes borderGlow {
    0% { opacity: 0.5; }
    100% { opacity: 1; }
}

.chat-header {
    display: flex;
    align-items: center;
    margin-bottom: v.$spacing-md;
    padding-bottom: v.$spacing-sm;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.bot-avatar {
    width: 50px;
    height: 50px;
    background: var(--gradient-primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    margin-right: v.$spacing-sm;
    @include m.pulse-animation(rgba(0, 102, 255, 0.3));
}

.bot-status {
    display: flex;
    align-items: center;
}

.status-indicator {
    width: 12px;
    height: 12px;
    background: var(--success-color);
    border-radius: 50%;
    margin-right: v.$spacing-xs;
    animation: statusBlink 1.5s ease-in-out infinite;
}

@keyframes statusBlink {
    0%, 100% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.3; transform: scale(1.2); }
}

.chat-messages {
    height: 300px;
    overflow: hidden;
    position: relative;
}

.message {
    margin-bottom: 1.5rem;
    opacity: 0;
    transform: translateY(20px);
    animation: messageAppear 0.8s ease-out forwards;

    &.user {
        text-align: right;
    }

    &.bot {
        text-align: left;
    }
}

.message-bubble {
    display: inline-block;
    padding: v.$spacing-sm 1.5rem;
    border-radius: v.$radius-md;
    max-width: 80%;
    font-size: 1rem;
    line-height: 1.4;
}

.user .message-bubble {
    background: var(--gradient-primary);
    color: white;
}

.bot .message-bubble {
    @include m.glassmorphism(0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: var(--text-primary);
}

@keyframes messageAppear {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.typing-indicator {
    display: flex;
    align-items: center;
    margin-top: v.$spacing-sm;
    opacity: 0;
}

.typing-dots {
    display: flex;
    gap: 4px;
    margin-left: v.$spacing-sm;
}

.typing-dot {
    width: 8px;
    height: 8px;
    background: var(--secondary-color);
    border-radius: 50%;
    animation: typingBounce 1.4s ease-in-out infinite both;

    &:nth-child(1) { animation-delay: -0.32s; }
    &:nth-child(2) { animation-delay: -0.16s; }
}

@keyframes typingBounce {
    0%, 80%, 100% { transform: scale(0); opacity: 0.5; }
    40% { transform: scale(1); opacity: 1; }
}

.results-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: v.$spacing-lg;
    margin-top: v.$spacing-lg;
}

.result-card {
    @include m.glassmorphism();
    border-radius: 25px;
    padding: v.$spacing-lg;
    text-align: center;
    position: relative;
    overflow: hidden;
    @include m.card-hover(1.02, rgba(0, 255, 136, 0.2));
    @include m.rotating-border(rgba(0, 255, 136, 0.1));

    &:hover {
        border-color: rgba(0, 255, 136, 0.4);
    }
}

.result-number {
    font-size: 4rem;
    font-weight: 900;
    @include m.gradient-text(var(--gradient-success));
    position: relative;
    z-index: 2;
    display: block;
    margin-bottom: v.$spacing-sm;
}

.result-label {
    font-size: 1.2rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: v.$spacing-sm;
    position: relative;
    z-index: 2;
}

.result-description {
    color: var(--text-secondary);
    line-height: 1.6;
    position: relative;
    z-index: 2;
}

.cta-section {
    padding: v.$spacing-xl v.$spacing-md;
    background: var(--gradient-dark);
    text-align: center;
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        animation: ctaPulse 6s ease-in-out infinite alternate;
    }
}

@keyframes ctaPulse {
    0% { transform: scale(1); opacity: 0.3; }
    100% { transform: scale(1.5); opacity: 0.6; }
}

.cta-buttons {
    display: flex;
    gap: v.$spacing-md;
    justify-content: center;
    flex-wrap: wrap;
}

@include m.mobile {
    .comparison-container {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .vs-divider {
        transform: rotate(90deg);
        font-size: 3rem;
    }

    .before-card, .after-card {
        padding: v.$spacing-md;
    }

    .results-grid {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .cta-buttons {
        flex-direction: column;
        align-items: center;
    }
}