@use '../base/variables' as v;
@use '../base/mixins' as m;

// ============================================
// BARRA DE HERRAMIENTAS LATERAL DEL EDITOR
// ============================================

.editor-sidebar-toolbar {
    position: fixed !important;
    left: 20px; /* Distancia desde el borde izquierdo */
    top: 50%; /* Centrada verticalmente */
    transform: translateY(-50%); /* Ajuste para centrado perfecto */
    width: 310px;
    min-width: 280px;
    max-height: calc(100vh - 40px); /* Altura mÃ¡xima menos margen */
    overflow-y: auto;
    z-index: 1000; /* Asegura que estÃ© por encima de otros elementos */
    
    /* Estilo glassmorphism */
    @include m.glassmorphism();
    border-radius: v.$radius-lg;
    padding: v.$spacing-md;
    
    /* TransiciÃ³n suave */
    transition: all 0.3s ease;
    
    &.disabled {
        opacity: 0.5;
        pointer-events: none;
    }
    
    // Secciones de la toolbar
    .toolbar-section {
        margin-bottom: v.$spacing-lg;
        
        &:last-child {
            margin-bottom: 0;
        }
        
        h4 {
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            font-size: 1rem;
            font-weight: 600;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            padding-bottom: v.$spacing-xs;
        }
    }
    
    // Grupos de herramientas
    .toolbar-group {
        display: flex;
        flex-direction: column;
        gap: v.$spacing-xs;
    }
    
    // Filas de herramientas
    .toolbar-row {
        display: flex;
        align-items: center;
        gap: v.$spacing-xs;
        flex-wrap: wrap;
        
        label {
            color: var(--text-secondary);
            font-size: 0.85rem;
            font-weight: 500;
            min-width: 60px;
            margin: 0;
        }
    }
    
    // Botones de la toolbar
    .toolbar-btn {
        background: rgba(255, 255, 255, 0.1);
        border: 1px solid rgba(255, 255, 255, 0.2);
        color: var(--text-primary);
        padding: v.$spacing-xs v.$spacing-sm;
        border-radius: v.$radius-sm;
        cursor: pointer;
        transition: all v.$transition-normal;
        font-size: 0.85rem;
        min-width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        
        &:hover {
            background: var(--primary-color);
            border-color: var(--primary-color);
            transform: translateY(-1px);
        }
        
        &.active {
            background: var(--primary-color);
            border-color: var(--primary-color);
            color: white;
        }
        
        &.danger {
            &:hover {
                background: var(--danger-color);
                border-color: var(--danger-color);
            }
            
            &.active {
                background: var(--danger-color);
                border-color: var(--danger-color);
            }
        }
    }
    
    // Selectores de la toolbar
    .toolbar-select {
        background: rgba(255, 255, 255, 0.1);
        border: 1px solid rgba(255, 255, 255, 0.2);
        color: var(--text-primary);
        padding: v.$spacing-xs;
        border-radius: v.$radius-sm;
        font-size: 0.85rem;
        cursor: pointer;
        flex: 1;
        min-width: 100px;
        
        &:focus {
            outline: none;
            border-color: var(--primary-color);
            box-shadow: 0 0 0 2px rgba(0, 102, 255, 0.2);
        }
        
        option {
            background: var(--dark-secondary);
            color: var(--text-primary);
        }
    }
}

// ============================================
// RESPONSIVE DESIGN PARA LA SIDEBAR
// ============================================

/* Para pantallas mÃ¡s pequeÃ±as, volver al comportamiento responsive */
@media (max-width: 1200px) {
    .editor-sidebar-toolbar {
        position: relative !important;
        left: auto !important;
        top: auto !important;
        transform: none !important;
        width: 100% !important;
        min-width: 100% !important;
        margin-bottom: v.$spacing-lg;
        max-height: none;
        
        .toolbar-row {
            justify-content: space-between;
            
            label {
                min-width: auto;
            }
        }
        
        .toolbar-section {
            margin-bottom: v.$spacing-md;
        }
    }
}

@include m.mobile {
    .editor-sidebar-toolbar {
        position: static !important;
        width: 100% !important;
        min-width: 100% !important;
        order: -1;
        max-height: none;
        
        .toolbar-row {
            justify-content: space-between;
            
            label {
                min-width: auto;
            }
        }
        
        .toolbar-section {
            margin-bottom: v.$spacing-md;
        }
    }
}