@use '../base/variables' as v;
@use '../base/mixins' as m;

.blog-form {
    display: flex;
    gap: v.$spacing-lg;
    align-items: flex-start;
}

.editor-content-area {
    flex: 1;
    min-width: 0;
}

.visual-editor {
    min-height: 400px;
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: v.$radius-lg;
    padding: v.$spacing-lg;
    max-width: 1200px;
    margin: 0 auto;
}

.content-block {
    position: relative;
    margin: v.$spacing-sm 0;
    padding: v.$spacing-sm;
    border-radius: v.$radius-md;
    transition: all v.$transition-normal;
    
    &.block-size-small {
        width: 50%;
    }
    
    &.block-size-medium {
        width: 80%;
    }
    
    &.block-size-large {
        width: 95%;
    }
    
    &.block-align-left {
        margin-left: 0;
        margin-right: auto;
    }
    
    &.block-align-center {
        margin-left: auto;
        margin-right: auto;
    }
    
    &.block-align-right {
        margin-left: auto;
        margin-right: 0;
    }
    
    &.selected {
        border: 2px solid var(--primary-color);
        background: rgba(0, 102, 255, 0.1);
        box-shadow: 0 0 0 4px rgba(0, 102, 255, 0.2);
    }

}

// NUEVOS ESTILOS: Sistema de botones + para agregar bloques
.block-plus-buttons {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    pointer-events: none; // Permite que el contenido del bloque sea clickeable
    
    .plus-btn {
        position: absolute;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        border: 2px solid var(--primary-color);
        background: var(--dark-bg);
        color: var(--primary-color);
        cursor: pointer;
        transition: all v.$transition-normal;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        font-weight: 700;
        pointer-events: auto; // Los botones sÃ­ deben ser clickeables
        box-shadow: 0 4px 12px rgba(0, 102, 255, 0.3);
        
        &:hover {
            background: var(--primary-color);
            color: white;
            transform: scale(1.1);
            box-shadow: 0 6px 20px rgba(0, 102, 255, 0.5);
        }
        
        &:active {
            transform: scale(0.95);
        }
        
        .plus-icon {
            line-height: 1;
            user-select: none;
        }
    }
    
    // NUEVO: BotÃ³n de eliminar (X)
    .delete-btn {
        position: absolute;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        border: 2px solid var(--danger-color);
        background: var(--dark-bg);
        color: var(--danger-color);
        cursor: pointer;
        transition: all v.$transition-normal;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        font-weight: 700;
        pointer-events: auto;
        box-shadow: 0 4px 12px rgba(255, 71, 87, 0.3);
        top: -14px;
        right: -14px;
        
        &:hover {
            background: var(--danger-color);
            color: white;
            transform: scale(1.1);
            box-shadow: 0 6px 20px rgba(255, 71, 87, 0.5);
        }
        
        &:active {
            transform: scale(0.95);
        }
        
        .delete-icon {
            line-height: 1;
            user-select: none;
            font-size: 18px;
        }
    }
    
    // Posicionamiento de los botones + - CORREGIDO
    .plus-btn-top {
        top: -16px;
        left: 50%;
        transform: translateX(-50%);
    }
    
    .plus-btn-bottom {
        bottom: -16px;
        left: 50%;
        transform: translateX(-50%);
    }
    
    .plus-btn-left {
        left: -16px;
        top: 50%;
        transform: translateY(-50%);
    }
    
    .plus-btn-right {
        right: -16px;
        top: 50%;
        transform: translateY(-50%);
    }
}

// NUEVOS ESTILOS: Modal para seleccionar tipo de contenido
.add-content-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    
    .modal-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(10, 10, 15, 0.8);
        backdrop-filter: blur(10px);
        animation: fadeIn 0.3s ease-out;
    }
    
    .modal-content {
        position: relative;
        background: var(--dark-secondary);
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: v.$radius-lg;
        padding: v.$spacing-lg;
        max-width: 500px;
        width: 90%;
        max-height: 90vh;
        overflow-y: auto;
        animation: slideInUp 0.3s ease-out;
        box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
        
        h3 {
            color: var(--text-primary);
            margin-bottom: v.$spacing-md;
            text-align: center;
            font-size: 1.2rem;
            font-weight: 700;
        }
        
        .content-type-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
            gap: v.$spacing-sm;
            margin-bottom: v.$spacing-lg;
        }
        
        .content-type-btn {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: v.$spacing-xs;
            padding: v.$spacing-md v.$spacing-sm;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-md;
            background: rgba(255, 255, 255, 0.05);
            color: var(--text-primary);
            cursor: pointer;
            transition: all v.$transition-normal;
            
            &:hover {
                border-color: var(--primary-color);
                background: rgba(0, 102, 255, 0.1);
                transform: translateY(-2px);
                box-shadow: 0 8px 20px rgba(0, 102, 255, 0.2);
            }
            
            .icon {
                font-size: 24px;
                line-height: 1;
            }
            
            .label {
                font-size: 0.85rem;
                font-weight: 600;
                text-align: center;
            }
        }
        
        .modal-close {
            width: 100%;
            padding: v.$spacing-sm v.$spacing-md;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-md;
            background: transparent;
            color: var(--text-secondary);
            cursor: pointer;
            transition: all v.$transition-normal;
            font-size: 0.9rem;
            
            &:hover {
                border-color: var(--danger-color);
                color: var(--danger-color);
            }
        }
    }
}

.text-content {
    min-height: 80px;
    outline: none;
    color: var(--text-primary);
    line-height: 1.6;
    padding: v.$spacing-sm;
    border-radius: v.$radius-sm;
    
    &:empty::before {
        content: attr(placeholder);
        color: var(--text-secondary);
        opacity: 0.7;
        font-style: italic;
    }
    
    &.text-align-left {
        text-align: left;
    }
    
    &.text-align-center {
        text-align: center;
    }
    
    &.text-align-right {
        text-align: right;
    }
    
    span {
        &.color-primary { color: var(--primary-color); }
        &.color-secondary { color: var(--secondary-color); }
        &.color-accent { color: var(--accent-color); }
        &.color-success { color: var(--success-color); }
        &.color-warning { color: var(--warning-color); }
        &.color-danger { color: var(--danger-color); }
        
        &.font-small { font-size: 0.9rem; }
        &.font-normal { font-size: 1rem; }
        &.font-large { font-size: 1.3rem; }
        &.font-xl { font-size: 1.6rem; }
    }
    
    a {
        color: var(--primary-color);
        text-decoration: underline;
        
        &:hover {
            color: var(--secondary-color);
        }
    }
}

.add-content-btn {
    text-align: center;
    margin: v.$spacing-lg 0;
    
    .main-add-btn {
        background: var(--gradient-primary);
        border: none;
        color: white;
        padding: v.$spacing-md v.$spacing-lg;
        border-radius: v.$radius-lg;
        font-size: 1.1rem;
        font-weight: 600;
        cursor: pointer;
        transition: all v.$transition-normal;
        
        &:hover {
            transform: translateY(-2px);
            box-shadow: 0 8px 25px rgba(0, 102, 255, 0.3);
        }
    }
    
    .content-options {
        display: flex;
        justify-content: center;
        gap: v.$spacing-sm;
        margin-top: v.$spacing-md;
        flex-wrap: wrap;
        
        button {
            background: rgba(255, 255, 255, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            color: var(--text-primary);
            padding: v.$spacing-sm v.$spacing-md;
            border-radius: v.$radius-md;
            cursor: pointer;
            transition: all v.$transition-normal;
            font-size: 0.9rem;
            
            &:hover {
                background: var(--primary-color);
                border-color: var(--primary-color);
                transform: translateY(-2px);
            }
        }
    }
}

.content-image {
    text-align: center;
    
    .image-upload {
        .upload-placeholder {
            border: 2px dashed rgba(255, 255, 255, 0.3);
            border-radius: v.$radius-md;
            padding: v.$spacing-lg;
            cursor: pointer;
            transition: all v.$transition-normal;
            color: var(--text-secondary);
            
            &:hover {
                border-color: var(--primary-color);
                background: rgba(0, 102, 255, 0.1);
                color: var(--text-primary);
            }
        }
    }
    
    img {
        max-width: 100%;
        height: auto;
        border-radius: v.$radius-sm;
        cursor: pointer;
        
        &:hover {
            opacity: 0.8;
        }
    }
}

.content-gif {
    .gif-embed {
        .gif-url {
            width: 100%;
            padding: v.$spacing-sm;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-sm;
            background: rgba(255, 255, 255, 0.05);
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
        
        .gif-preview {
            img, iframe {
                max-width: 100%;
                height: auto;
                border-radius: v.$radius-sm;
            }
        }
    }
}

.content-video {
    .video-embed {
        .video-url {
            width: 100%;
            padding: v.$spacing-sm;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-sm;
            background: rgba(255, 255, 255, 0.05);
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
        
        .video-preview {
            iframe {
                width: 100%;
                height: 315px;
                border-radius: v.$radius-sm;
                border: none;
            }
        }
    }
}

.content-code {
    .code-editor {
        background: var(--dark-bg);
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: v.$radius-sm;
        overflow: hidden;
        
        .code-header {
            background: rgba(255, 255, 255, 0.05);
            padding: v.$spacing-xs v.$spacing-sm;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            
            select {
                background: transparent;
                border: none;
                color: var(--text-primary);
                font-size: 0.85rem;
                cursor: pointer;
                
                &:focus {
                    outline: none;
                }
            }
        }
        
        textarea {
            width: 100%;
            min-height: 200px;
            padding: v.$spacing-md;
            border: none;
            background: transparent;
            color: var(--text-primary);
            font-family: 'Courier New', monospace;
            font-size: 0.9rem;
            resize: vertical;
            
            &:focus {
                outline: none;
            }
        }
    }
}

.content-quote {
    .quote-editor {
        border-left: 4px solid var(--primary-color);
        padding-left: v.$spacing-md;
        background: rgba(0, 102, 255, 0.05);
        border-radius: v.$radius-sm;
        
        .quote-text {
            font-size: 1.1rem;
            font-style: italic;
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            outline: none;
            min-height: 60px;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
            }
        }
        
        .quote-author {
            font-size: 0.9rem;
            color: var(--text-secondary);
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                opacity: 0.7;
            }
        }
    }
}

.content-card {
    .card-editor {
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: v.$radius-md;
        background: rgba(255, 255, 255, 0.05);
        overflow: hidden;
        
        .card-header {
            background: rgba(255, 255, 255, 0.05);
            padding: v.$spacing-md;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            font-weight: 600;
            color: var(--text-primary);
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
                font-weight: normal;
            }
        }
        
        .card-body {
            padding: v.$spacing-md;
            color: var(--text-primary);
            line-height: 1.6;
            outline: none;
            min-height: 80px;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
            }
        }
        
        .card-footer {
            padding: v.$spacing-sm v.$spacing-md;
            background: rgba(255, 255, 255, 0.02);
            border-top: 1px solid rgba(255, 255, 255, 0.1);
            color: var(--text-secondary);
            font-size: 0.9rem;
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                opacity: 0.7;
            }
        }
    }
}

.content-row {
    display: flex;
    gap: v.$spacing-md;
    margin: 0;
    
    .content-column {
        flex: 1;
        min-width: 0;
    }
}

// NUEVOS ESTILOS: Estados de carga y error
.upload-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: v.$spacing-sm;
    padding: v.$spacing-lg;
    color: var(--text-secondary);
    
    .loading-spinner {
        width: 24px;
        height: 24px;
        border: 2px solid rgba(255, 255, 255, 0.3);
        border-top: 2px solid var(--primary-color);
        border-radius: 50%;
        animation: spin 1s linear infinite;
    }
}

.upload-error {
    color: var(--danger-color);
    text-align: center;
    padding: v.$spacing-md;
    background: rgba(255, 71, 87, 0.1);
    border: 1px solid rgba(255, 71, 87, 0.3);
    border-radius: v.$radius-sm;
}

// Animaciones
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes blockSelect {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.02);
    }
    100% {
        transform: scale(1);
    }
}

.content-block.selected {
    animation: blockSelect 0.3s ease-in-out;
}

// Responsive design
@media (max-width: 1200px) {
    .editor-content-area {
        margin-left: 0;
    }
    
    .blog-form {
        flex-direction: row;
    }
}

@include m.mobile {
    .blog-form {
        flex-direction: column;
    }
    
    .editor-content-area {
        margin-left: 0;
    }
    
    .visual-editor {
        padding: v.$spacing-md;
    }
    
    .content-block {
        &.block-size-small,
        &.block-size-medium,
        &.block-size-large {
            width: 100%;
        }
    }
    
    .content-row {
        flex-direction: column;
        gap: v.$spacing-sm;
    }
    
    // Ajustar botones + y X en mÃ³vil
    .block-plus-buttons {
        .plus-btn {
            width: 28px;
            height: 28px;
            font-size: 16px;
        }
        
        .delete-btn {
            width: 24px;
            height: 24px;
            font-size: 14px;
            top: -12px;
            right: -12px;
            
            .delete-icon {
                font-size: 16px;
            }
        }
        
        .plus-btn-top {
            top: -14px;
        }
        
        .plus-btn-bottom {
            bottom: -14px;
        }
        
        .plus-btn-left {
            left: -14px;
        }
        
        .plus-btn-right {
            right: -14px;
        }
    }
    
    // Ajustar modal en mÃ³vil
    .add-content-modal {
        .modal-content {
            width: 95%;
            padding: v.$spacing-md;
            
            .content-type-grid {
                grid-template-columns: repeat(2, 1fr);
                gap: v.$spacing-xs;
            }
            
            .content-type-btn {
                padding: v.$spacing-sm;
                
                .icon {
                    font-size: 20px;
                }
                
                .label {
                    font-size: 0.8rem;
                }
            }
        }
    }
    
    .add-content-btn {
        .content-options {
            justify-content: center;
            
            button {
                flex: 1;
                min-width: 120px;
            }
        }
    }
}