@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/globals' as g;

.timeline-hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: var(--gradient-dark);

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 30% 40%, rgba(0, 102, 255, 0.15) 0%, transparent 50%),
                    radial-gradient(circle at 70% 60%, rgba(255, 107, 53, 0.15) 0%, transparent 50%);
        animation: heroGlow 8s ease-in-out infinite alternate;
    }
}

@keyframes heroGlow {
    0% { opacity: 0.5; transform: scale(1); }
    100% { opacity: 1; transform: scale(1.1); }
}

.hero-content {
    text-align: center;
    max-width: 1000px;
    z-index: 2;
    position: relative;
    padding: v.$spacing-md;

    h1 {
        font-size: clamp(3rem, 8vw, 6rem);
        font-weight: 900;
        margin-bottom: v.$spacing-md;
        background: linear-gradient(135deg, #ffffff, #00d4ff, #0066ff);
        @include m.gradient-text(linear-gradient(135deg, #ffffff, #00d4ff, #0066ff));
        animation: textShimmer 3s ease-in-out infinite;
    }
}

@keyframes textShimmer {
    0%, 100% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
}

.hero-subtitle {
    font-size: 1.5rem;
    color: var(--text-secondary);
    margin-bottom: v.$spacing-lg;
    animation: fadeInUp 1s ease-out 0.5s both;
}

.timeline-container {
    padding: v.$spacing-xl v.$spacing-md;
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
}

.timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;

    &::before {
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        bottom: 0;
        width: 4px;
        background: var(--gradient-primary);
        transform: translateX(-50%);
        border-radius: 2px;
        box-shadow: 0 0 20px rgba(0, 102, 255, 0.5);
    }
}

.timeline-item {
    position: relative;
    margin-bottom: 6rem;
    opacity: 0;
    transform: translateY(50px);
    transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);

    &.visible {
        opacity: 1;
        transform: translateY(0);
    }

    &:nth-child(odd) .timeline-content {
        margin-right: 60%;
        text-align: right;
    }

    &:nth-child(even) .timeline-content {
        margin-left: 60%;
        text-align: left;
    }
}

.timeline-marker {
    position: absolute;
    left: 50%;
    top: 30px;
    width: 40px;
    height: 40px;
    background: var(--gradient-primary);
    border-radius: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    font-weight: bold;
    box-shadow: 0 0 30px rgba(0, 102, 255, 0.6);
    z-index: 10;
    transition: all v.$transition-normal;
}

.timeline-item:hover .timeline-marker {
    transform: translateX(-50%) scale(1.2);
    box-shadow: 0 0 40px rgba(0, 102, 255, 0.8);
}

.timeline-content {
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    padding: v.$spacing-lg;
    position: relative;
    transition: all v.$transition-normal;
    cursor: pointer;

    &:hover {
        background: rgba(255, 255, 255, 0.08);
        transform: translateY(-10px);
        box-shadow: 0 20px 60px rgba(0, 102, 255, 0.2);
    }

    &::before {
        content: '';
        position: absolute;
        top: 40px;
        width: 0;
        height: 0;
        border: 15px solid transparent;
    }
}

.timeline-item:nth-child(odd) .timeline-content::before {
    right: -30px;
    border-left-color: rgba(255, 255, 255, 0.1);
}

.timeline-item:nth-child(even) .timeline-content::before {
    left: -30px;
    border-right-color: rgba(255, 255, 255, 0.1);
}

.timeline-year {
    font-size: 3rem;
    font-weight: 900;
    @include m.gradient-text(var(--gradient-accent));
    margin-bottom: v.$spacing-sm;
}

.timeline-title-text {
    font-size: 1.8rem;
    font-weight: 700;
    margin-bottom: v.$spacing-sm;
    color: var(--text-primary);
}

.timeline-description {
    color: var(--text-secondary);
    font-size: 1.1rem;
    line-height: 1.6;
}

.values-section {
    padding: v.$spacing-xl v.$spacing-md;
    background: var(--gradient-dark);
    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hexagon" width="10" height="8.66" patternUnits="userSpaceOnUse"><polygon points="5,0 9.33,2.5 9.33,7.5 5,10 0.67,7.5 0.67,2.5" fill="none" stroke="%23ffffff" stroke-width="0.3" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23hexagon)"/></svg>');
        opacity: 0.3;
    }
}

.values-container {
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

.values-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: v.$spacing-lg;
    margin-top: v.$spacing-lg;
}

.value-card {
    @include m.glassmorphism();
    border-radius: 25px;
    padding: v.$spacing-lg;
    text-align: center;
    @include m.card-hover(1.02, rgba(0, 102, 255, 0.3));
    position: relative;
    overflow: hidden;
    @include m.rotating-border();

    &:hover {
        border-color: rgba(0, 102, 255, 0.4);
    }
}

.value-icon {
    width: 100px;
    height: 100px;
    background: var(--gradient-primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    margin: 0 auto v.$spacing-md;
    position: relative;
    z-index: 2;
    transition: all v.$transition-normal;
}

.value-card:hover .value-icon {
    transform: rotateY(180deg);
}

.value-card h3 {
    font-size: 1.8rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    color: var(--text-primary);
    position: relative;
    z-index: 2;
}

.value-card p {
    color: var(--text-secondary);
    line-height: 1.6;
    position: relative;
    z-index: 2;
}

.team-section {
    padding: v.$spacing-xl v.$spacing-md;
    max-width: v.$desktop;
    margin: 0 auto;
}

.achievement-showcase {
    @include m.glassmorphism();
    border-radius: 30px;
    padding: v.$spacing-lg;
    text-align: center;
    position: relative;
    overflow: hidden;
    margin-top: v.$spacing-lg;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
        animation: shimmer 3s ease-in-out infinite;
    }
}

@keyframes shimmer {
    0% { left: -100%; }
    50% { left: 100%; }
    100% { left: 100%; }
}

.achievement-icon {
    font-size: 5rem;
    margin-bottom: v.$spacing-md;
    animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
    40% { transform: translateY(-10px); }
    60% { transform: translateY(-5px); }
}

.achievement-title {
    font-size: 2.5rem;
    font-weight: 900;
    margin-bottom: 1.5rem;
    @include m.gradient-text(var(--gradient-accent));
}

.achievement-description {
    font-size: 1.2rem;
    color: var(--text-secondary);
    line-height: 1.6;
}

.stats-container {
    @extend .grid-2;
    gap: v.$spacing-md;
    margin-top: v.$spacing-lg;
}

.stat-item {
    text-align: center;
    padding: v.$spacing-md;
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    transition: all v.$transition-normal;
    cursor: pointer;

    &:hover {
        transform: translateY(-5px);
        background: rgba(255, 255, 255, 0.08);
        box-shadow: 0 15px 40px rgba(0, 102, 255, 0.2);
    }
}
.stat-posfix{
    display: flex;
    justify-content: center;
    align-items: baseline;
    gap: 0.2rem;
}
.stat-number {
    font-size: 3rem;
    font-weight: 900;
    @include m.gradient-text(var(--gradient-primary));
    display: block;
}

.stat-label {
    color: var(--text-secondary);
    font-size: 1rem;
    margin-top: v.$spacing-xs;
}

@include m.mobile {
    .timeline::before {
        left: 30px;
    }

    .timeline-marker {
        left: 30px;
    }

    .timeline-item:nth-child(odd) .timeline-content,
    .timeline-item:nth-child(even) .timeline-content {
        margin-left: 80px;
        margin-right: 0;
        text-align: left;
    }

    .timeline-item:nth-child(odd) .timeline-content::before,
    .timeline-item:nth-child(even) .timeline-content::before {
        left: -30px;
        right: auto;
        border-right-color: rgba(255, 255, 255, 0.1);
        border-left-color: transparent;
    }

    .values-grid {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .timeline-content {
        padding: v.$spacing-md;
    }

    .achievement-showcase {
        padding: v.$spacing-md;
    }
}