@use '../base/variables' as v;
@use '../base/mixins' as m;

// Text Block Styles
.content-text {
    .text-toolbar {
        display: flex;
        gap: v.$spacing-xs;
        margin-bottom: v.$spacing-sm;
        padding: v.$spacing-xs;
        background: rgba(255, 255, 255, 0.05);
        border-radius: v.$radius-sm;
        flex-wrap: wrap;
        
        .format-btn {
            background: rgba(255, 255, 255, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            color: var(--text-primary);
            padding: v.$spacing-xs v.$spacing-sm;
            border-radius: v.$radius-sm;
            cursor: pointer;
            transition: all v.$transition-normal;
            font-size: 0.85rem;
            min-width: 32px;
            
            &:hover {
                background: var(--primary-color);
                border-color: var(--primary-color);
            }
        }
        
        .color-select, .size-select {
            background: rgba(255, 255, 255, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            color: var(--text-primary);
            padding: v.$spacing-xs;
            border-radius: v.$radius-sm;
            font-size: 0.85rem;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
    }
    
    .text-content {
        min-height: 100px;
        outline: none;
        color: var(--text-primary);
        line-height: 1.6;
        
        &:empty::before {
            content: attr(placeholder);
            color: var(--text-secondary);
            opacity: 0.7;
            font-style: italic;
        }
        
        // Inline styling for formatted text
        span {
            &.color-primary { color: var(--primary-color); }
            &.color-secondary { color: var(--secondary-color); }
            &.color-accent { color: var(--accent-color); }
            &.color-success { color: var(--success-color); }
            &.color-warning { color: var(--warning-color); }
            &.color-danger { color: var(--danger-color); }
            
            &.font-small { font-size: 0.9rem; }
            &.font-normal { font-size: 1rem; }
            &.font-large { font-size: 1.3rem; }
            &.font-xl { font-size: 1.6rem; }
        }
        
        a {
            color: var(--primary-color);
            text-decoration: underline;
            
            &:hover {
                color: var(--secondary-color);
            }
        }
    }
}

// Image Block Styles
.content-image {
    text-align: center;
    
    .image-upload {
        .upload-placeholder {
            border: 2px dashed rgba(255, 255, 255, 0.3);
            border-radius: v.$radius-md;
            padding: v.$spacing-lg;
            cursor: pointer;
            transition: all v.$transition-normal;
            color: var(--text-secondary);
            
            &:hover {
                border-color: var(--primary-color);
                background: rgba(0, 102, 255, 0.1);
                color: var(--text-primary);
            }
        }
    }
    
    img {
        max-width: 100%;
        height: auto;
        border-radius: v.$radius-sm;
    }
}

// GIF Block Styles
.content-gif {
    text-align: center;
    
    .gif-embed {
        .gif-url {
            width: 100%;
            padding: v.$spacing-sm;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-sm;
            background: rgba(255, 255, 255, 0.05);
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
        
        .embed-gif-btn {
            background: var(--gradient-accent);
            color: white;
            border: none;
            padding: v.$spacing-sm v.$spacing-md;
            border-radius: v.$radius-sm;
            cursor: pointer;
            transition: all v.$transition-normal;
            font-weight: 600;
            
            &:hover {
                background: var(--gradient-primary);
                transform: translateY(-2px);
            }
        }
        
        .gif-preview {
            margin-top: v.$spacing-md;
            
            img {
                border-radius: v.$radius-sm;
                box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
                transition: transform v.$transition-normal;
                
                &:hover {
                    transform: scale(1.02);
                }
            }
        }
    }
    
    img {
        max-width: 100%;
        height: auto;
        border-radius: v.$radius-sm;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    }
}

// Code Block Styles
.content-code {
    .code-toolbar {
        margin-bottom: v.$spacing-sm;
        
        .language-select {
            background: rgba(255, 255, 255, 0.1);
            border: 1px solid rgba(255, 255, 255, 0.2);
            color: var(--text-primary);
            padding: v.$spacing-xs v.$spacing-sm;
            border-radius: v.$radius-sm;
            font-size: 0.85rem;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
    }
    
    pre {
        background: rgba(0, 0, 0, 0.3);
        border-radius: v.$radius-sm;
        padding: v.$spacing-md;
        margin: 0;
        overflow-x: auto;
        
        code {
            color: var(--secondary-color);
            font-family: 'Courier New', monospace;
            font-size: 0.9rem;
            line-height: 1.4;
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
                font-style: italic;
            }
        }
    }
}

// Quote Block Styles
.content-quote {
    blockquote {
        border-left: 4px solid var(--primary-color);
        padding: v.$spacing-md v.$spacing-lg;
        margin: 0;
        background: rgba(0, 102, 255, 0.1);
        border-radius: 0 v.$radius-sm v.$radius-sm 0;
        font-style: italic;
        
        p {
            margin: 0 0 v.$spacing-sm;
            font-size: 1.2rem;
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
                font-style: italic;
            }
        }
        
        cite {
            font-size: 0.9rem;
            color: var(--text-secondary);
            font-style: normal;
            outline: none;
            display: block;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.5;
            }
        }
    }
}

// Video Block Styles
.content-video {
    .video-embed {
        .video-url {
            width: 100%;
            padding: v.$spacing-sm;
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: v.$radius-sm;
            background: rgba(255, 255, 255, 0.05);
            color: var(--text-primary);
            margin-bottom: v.$spacing-sm;
            
            &:focus {
                outline: none;
                border-color: var(--primary-color);
            }
        }
        
        button {
            background: var(--primary-color);
            color: white;
            border: none;
            padding: v.$spacing-sm v.$spacing-md;
            border-radius: v.$radius-sm;
            cursor: pointer;
            transition: all v.$transition-normal;
            
            &:hover {
                background: var(--secondary-color);
            }
        }
        
        .video-preview {
            margin-top: v.$spacing-md;
            
            iframe {
                border-radius: v.$radius-sm;
            }
        }
    }
}

// Card Block Styles
.content-card {
    .content-card {
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: v.$radius-md;
        overflow: hidden;
        
        .card-header {
            background: rgba(255, 255, 255, 0.1);
            padding: v.$spacing-md;
            font-weight: 600;
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
                font-style: italic;
            }
        }
        
        .card-body {
            padding: v.$spacing-md;
            min-height: 100px;
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.7;
                font-style: italic;
            }
        }
        
        .card-footer {
            background: rgba(255, 255, 255, 0.05);
            padding: v.$spacing-sm v.$spacing-md;
            font-size: 0.9rem;
            color: var(--text-secondary);
            outline: none;
            
            &:empty::before {
                content: attr(placeholder);
                color: var(--text-secondary);
                opacity: 0.5;
                font-style: italic;
            }
        }
    }
}