@use 'base/variables' as v;
@use 'base/mixins' as m;

.mobile-menu-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
    padding: v.$spacing-xs;

    span {
        width: 25px;
        height: 3px;
        background: var(--text-primary);
        margin: 2px 0;
        transition: v.$transition-normal;
    }
}

.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: v.$spacing-md;
    position: relative;
}

.hero-content {
    max-width: 1200px;
    z-index: 2;

    h1 {
        font-size: clamp(2.8rem, 8vw, 8rem);
        font-weight: 900;
        line-height: 1.1;
        margin: v.$spacing-xl auto;
        background: linear-gradient(135deg, #ffffff, #b0b3c7);
        @include m.gradient-text(linear-gradient(135deg, #ffffff, #b0b3c7));
        animation: fadeInUp 1s ease-out;

        .highlight {
            @include m.gradient-text(var(--gradient-primary));
        }
    }

    p {
        font-size: clamp(1.2rem, 3vw, 1.5rem);
        color: var(--text-secondary);
        margin-bottom: v.$spacing-lg;
        max-width: 700px;
        margin-left: auto;
        margin-right: auto;
        animation: fadeInUp 1s ease-out 0.2s both;
    }
}

.hero-buttons {
    display: flex;
    gap: 1.5rem;
    justify-content: center;
    flex-wrap: wrap;
    animation: fadeInUp 1s ease-out 0.4s both;
}

/* Particle Effect */
.particles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.particle {
    position: absolute;
    width: 2px;
    height: 2px;
    background: var(--secondary-color);
    border-radius: 50%;
    opacity: 0.6;
    animation: particleFloat 20s linear infinite;
}

@keyframes particleFloat {
    0% {
        transform: translateY(100vh) rotate(0deg);
        opacity: 0;
    }
    10% {
        opacity: 0.6;
    }
    90% {
        opacity: 0.6;
    }
    100% {
        transform: translateY(-100vh) rotate(360deg);
        opacity: 0;
    }
}

/* Interactive Elements */
.interactive-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: -1;
}

.floating-element {
    position: absolute;
    background: var(--gradient-primary);
    border-radius: 50%;
    opacity: 0.1;
    @include m.floating-animation();

    &:nth-child(1) {
        width: 60px;
        height: 60px;
        top: 20%;
        left: 10%;
        animation-delay: 0s;
    }

    &:nth-child(2) {
        width: 80px;
        height: 80px;
        top: 60%;
        right: 15%;
        animation-delay: 2s;
    }

    &:nth-child(3) {
        width: 40px;
        height: 40px;
        bottom: 30%;
        left: 20%;
        animation-delay: 4s;
        background: var(--gradient-accent);
    }
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: v.$spacing-lg;
    margin-top: v.$spacing-lg;
    
    @include m.desktop {
        grid-template-columns: repeat(3, 1fr);
    }
}

.feature-card {
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    padding: 2.5rem;
    @include m.card-hover();
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--gradient-primary);
        opacity: 0;
        transition: opacity v.$transition-normal;
        z-index: -1;
    }

    &:hover::before {
        opacity: 0.1;
    }
}

.feature-icon {
    width: 80px;
    height: 80px;
    background: var(--gradient-primary);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

.feature-card h3 {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: v.$spacing-sm;
    color: var(--text-primary);
}

.feature-card p {
    color: var(--text-secondary);
    line-height: 1.6;
}

/* CTA Section */
.cta-section {
    background: var(--gradient-dark);
    padding: 6rem 2rem;
    text-align: center;
    position: relative;
}

.cta-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="%23ffffff" stroke-width="0.5" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
    opacity: 0.3;
}

.cta-content {
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    z-index: 2;

    h2 {
        font-size: clamp(2rem, 5vw, 3.5rem);
        font-weight: 900;
        margin-bottom: 1.5rem;
        @include m.gradient-text(var(--gradient-primary));
    }

    p {
        font-size: 1.2rem;
        color: var(--text-secondary);
        margin-bottom: 2.5rem;
    }
}