@charset "UTF-8";
:root {
  --primary-color: #0066ff;
  --secondary-color: #00d4ff;
  --accent-color: #ff6b35;
  --success-color: #00ff88;
  --warning-color: #ffaa00;
  --danger-color: #ff4757;
  --whatsapp-color: #25d366;
  --facebook-color: #1877f2;
  --instagram-color: #e4405f;
  --dark-bg: #0a0a0f;
  --dark-secondary: #1a1a2e;
  --dark-tertiary: #16213e;
  --text-primary: #ffffff;
  --text-secondary: #b0b3c7;
  --gradient-primary: linear-gradient(135deg, #0066ff, #00d4ff);
  --gradient-accent: linear-gradient(135deg, #ff6b35, #ff8e53);
  --gradient-success: linear-gradient(135deg, #00ff88, #00cc6a);
  --gradient-whatsapp: linear-gradient(135deg, #25d366, #128c7e);
  --gradient-facebook: linear-gradient(135deg, #1877f2, #42a5f5);
  --gradient-instagram: linear-gradient(135deg, #e4405f, #fd1d1d, #fcb045);
  --gradient-dark: linear-gradient(135deg, #0a0a0f, #1a1a2e);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  background: var(--dark-bg);
  color: var(--text-primary);
  overflow-x: hidden;
  line-height: 1.6;
}

@media (max-width: 767px) {
  .hero h1 {
    font-size: 3rem;
  }
  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  .features-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .feature-card {
    padding: 2rem;
  }
}
.bg-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: var(--gradient-dark);
}
.bg-animation::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 50%, rgba(0, 102, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 20%, rgba(0, 212, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 40% 80%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
  animation: backgroundPulse 20s ease-in-out infinite;
}

@keyframes backgroundPulse {
  0%, 100% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-20px) rotate(120deg);
  }
  66% {
    transform: translateY(10px) rotate(240deg);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}
.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

.section {
  padding: 8rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 900;
  text-align: center;
  margin-bottom: 4rem;
  padding-top: 12rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.section-title.regularPadding {
  padding-top: 0;
}

.section-subtitle {
  text-align: center;
  font-size: 1.3rem;
  color: var(--text-secondary);
  margin-bottom: 4rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.glow-cursor {
  position: absolute;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(0, 102, 255, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  pointer-events: none;
  transition: all 0.1s ease;
  z-index: -1;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  padding: 1rem 2.5rem;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
  display: inline-block;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 102, 255, 0.4);
}
.btn-primary::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.btn-primary:hover::before {
  left: 100%;
}

.btn-secondary {
  background: transparent;
  color: var(--text-primary);
  padding: 1rem 2.5rem;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}
.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.4);
  transform: translateY(-3px);
}

.card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  padding: 2rem;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}
.card:hover {
  transform: translateY(-15px) scale(1.02);
  box-shadow: 0 30px 80px rgba(0, 102, 255, 0.2);
}

.grid-2, .stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

@media (max-width: 767px) {
  .section {
    padding: 4rem 1rem;
  }
  .btn-primary,
  .btn-secondary {
    width: 100%;
    max-width: 300px;
    text-align: center;
  }
  .grid-2, .stats-container,
  .grid-3 {
    grid-template-columns: 1fr;
  }
  .visual-editor {
    transform: scale(0.8);
    transform-origin: top left;
    width: 125%;
    margin-left: 0;
    margin-right: 0;
  }
  .post-content {
    transform: scale(0.85);
    transform-origin: top left;
    width: 117.6%;
    margin-left: 0;
    margin-right: 0;
  }
  .content-row {
    display: flex !important;
    flex-direction: row !important;
    gap: 0.5rem;
    margin: 1rem 0;
  }
  .content-row .content-column {
    flex: 1;
    min-width: 0;
  }
  .content-block.block-size-small {
    width: 50% !important;
  }
  .content-block.block-size-medium {
    width: 80% !important;
  }
  .content-block.block-size-large {
    width: 95% !important;
  }
  .content-block.block-align-left {
    margin-left: 0;
    margin-right: auto;
  }
  .content-block.block-align-center {
    margin-left: auto;
    margin-right: auto;
  }
  .content-block.block-align-right {
    margin-left: auto;
    margin-right: 0;
  }
  .editor-sidebar-toolbar {
    transform: scale(0.8);
    transform-origin: top left;
    width: 125%;
    min-width: 125%;
    position: sticky;
    top: 1rem;
  }
  .content-video iframe {
    height: 200px;
  }
  .text-content {
    font-size: 0.95rem;
    line-height: 1.5;
  }
  .text-content span.font-small {
    font-size: 0.8rem !important;
  }
  .text-content span.font-normal {
    font-size: 0.95rem !important;
  }
  .text-content span.font-large {
    font-size: 1.1rem !important;
  }
  .text-content span.font-xl {
    font-size: 1.3rem !important;
  }
}
@media (max-width: 400px) {
  .visual-editor {
    transform: scale(0.7);
    width: 142.8%;
  }
  .post-content {
    transform: scale(0.75);
    width: 133.3%;
  }
  .editor-sidebar-toolbar {
    transform: scale(0.7);
    width: 142.8%;
  }
}
.error-hero {
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
}

.error-code {
  font-size: clamp(4rem, 15vw, 12rem);
  font-weight: 900;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: block;
  margin: 4rem 0;
  opacity: 0.8;
}

.error-title {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
}

.error-subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.error-actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.suggestions-section {
  padding: 4rem 2rem;
  background: rgba(255, 255, 255, 0.02);
}

.suggestion_text {
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .error-actions {
    flex-direction: column;
    align-items: center;
  }
  .error-actions .btn-primary,
  .error-actions .btn-secondary {
    width: 100%;
    max-width: 300px;
  }
}
.timeline-hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background: var(--gradient-dark);
}
.timeline-hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 40%, rgba(0, 102, 255, 0.15) 0%, transparent 50%), radial-gradient(circle at 70% 60%, rgba(255, 107, 53, 0.15) 0%, transparent 50%);
  animation: heroGlow 8s ease-in-out infinite alternate;
}

@keyframes heroGlow {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1.1);
  }
}
.hero-content {
  text-align: center;
  max-width: 1000px;
  z-index: 2;
  position: relative;
  padding: 2rem;
}
.hero-content h1 {
  font-size: clamp(3rem, 8vw, 6rem);
  font-weight: 900;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, #ffffff, #00d4ff, #0066ff);
  background: linear-gradient(135deg, #ffffff, #00d4ff, #0066ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: textShimmer 3s ease-in-out infinite;
}

@keyframes textShimmer {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
.hero-subtitle {
  font-size: 1.5rem;
  color: var(--text-secondary);
  margin-bottom: 4rem;
  animation: fadeInUp 1s ease-out 0.5s both;
}

.timeline-container {
  padding: 8rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
}

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}
.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: var(--gradient-primary);
  transform: translateX(-50%);
  border-radius: 2px;
  box-shadow: 0 0 20px rgba(0, 102, 255, 0.5);
}

.timeline-item {
  position: relative;
  margin-bottom: 6rem;
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}
.timeline-item.visible {
  opacity: 1;
  transform: translateY(0);
}
.timeline-item:nth-child(odd) .timeline-content {
  margin-right: 60%;
  text-align: right;
}
.timeline-item:nth-child(even) .timeline-content {
  margin-left: 60%;
  text-align: left;
}

.timeline-marker {
  position: absolute;
  left: 50%;
  top: 30px;
  width: 40px;
  height: 40px;
  background: var(--gradient-primary);
  border-radius: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: bold;
  box-shadow: 0 0 30px rgba(0, 102, 255, 0.6);
  z-index: 10;
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-marker {
  transform: translateX(-50%) scale(1.2);
  box-shadow: 0 0 40px rgba(0, 102, 255, 0.8);
}

.timeline-content {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 4rem;
  position: relative;
  transition: all 0.3s ease;
  cursor: pointer;
}
.timeline-content:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-10px);
  box-shadow: 0 20px 60px rgba(0, 102, 255, 0.2);
}
.timeline-content::before {
  content: "";
  position: absolute;
  top: 40px;
  width: 0;
  height: 0;
  border: 15px solid transparent;
}

.timeline-item:nth-child(odd) .timeline-content::before {
  right: -30px;
  border-left-color: rgba(255, 255, 255, 0.1);
}

.timeline-item:nth-child(even) .timeline-content::before {
  left: -30px;
  border-right-color: rgba(255, 255, 255, 0.1);
}

.timeline-year {
  font-size: 3rem;
  font-weight: 900;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
}

.timeline-title-text {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.timeline-description {
  color: var(--text-secondary);
  font-size: 1.1rem;
  line-height: 1.6;
}

.values-section {
  padding: 8rem 2rem;
  background: var(--gradient-dark);
  position: relative;
}
.values-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hexagon" width="10" height="8.66" patternUnits="userSpaceOnUse"><polygon points="5,0 9.33,2.5 9.33,7.5 5,10 0.67,7.5 0.67,2.5" fill="none" stroke="%23ffffff" stroke-width="0.3" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23hexagon)"/></svg>');
  opacity: 0.3;
}

.values-container {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 4rem;
  margin-top: 4rem;
}

.value-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 4rem;
  text-align: center;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}
.value-card:hover {
  transform: translateY(-15px) scale(1.02);
  box-shadow: 0 30px 80px rgba(0, 102, 255, 0.3);
}
.value-card {
  position: relative;
  overflow: hidden;
}
.value-card::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-radius: inherit;
  padding: 2px;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: xor;
  animation: borderRotate 3s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.value-card:hover::before {
  opacity: 1;
}
@keyframes borderRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.value-card:hover {
  border-color: rgba(0, 102, 255, 0.4);
}

.value-icon {
  width: 100px;
  height: 100px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  margin: 0 auto 2rem;
  position: relative;
  z-index: 2;
  transition: all 0.3s ease;
}

.value-card:hover .value-icon {
  transform: rotateY(180deg);
}

.value-card h3 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  position: relative;
  z-index: 2;
}

.value-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  position: relative;
  z-index: 2;
}

.team-section {
  padding: 8rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.achievement-showcase {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  padding: 4rem;
  text-align: center;
  position: relative;
  overflow: hidden;
  margin-top: 4rem;
}
.achievement-showcase::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0% {
    left: -100%;
  }
  50% {
    left: 100%;
  }
  100% {
    left: 100%;
  }
}
.achievement-icon {
  font-size: 5rem;
  margin-bottom: 2rem;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}
.achievement-title {
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 1.5rem;
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.achievement-description {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
}

.stats-container {
  gap: 2rem;
  margin-top: 4rem;
}

.stat-item {
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  transition: all 0.3s ease;
  cursor: pointer;
}
.stat-item:hover {
  transform: translateY(-5px);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 15px 40px rgba(0, 102, 255, 0.2);
}

.stat-posfix {
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 0.2rem;
}

.stat-number {
  font-size: 3rem;
  font-weight: 900;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: block;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 1rem;
  margin-top: 0.5rem;
}

@media (max-width: 767px) {
  .timeline::before {
    left: 30px;
  }
  .timeline-marker {
    left: 30px;
  }
  .timeline-item:nth-child(odd) .timeline-content,
  .timeline-item:nth-child(even) .timeline-content {
    margin-left: 80px;
    margin-right: 0;
    text-align: left;
  }
  .timeline-item:nth-child(odd) .timeline-content::before,
  .timeline-item:nth-child(even) .timeline-content::before {
    left: -30px;
    right: auto;
    border-right-color: rgba(255, 255, 255, 0.1);
    border-left-color: transparent;
  }
  .values-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .timeline-content {
    padding: 2rem;
  }
  .achievement-showcase {
    padding: 2rem;
  }
}
.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: var(--dark-bg);
  position: relative;
}
.auth-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
  animation: authBackground 8s ease-in-out infinite alternate;
}

@keyframes authBackground {
  0% {
    opacity: 0.3;
    transform: scale(1);
  }
  100% {
    opacity: 0.6;
    transform: scale(1.1);
  }
}
.auth-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 8rem;
  width: 100%;
  max-width: 800px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.auth-container::before {
