@media (min-width: 768px) and (max-width: 1023px) {
  .nav-links {
    gap: 1rem;
  }
  .nav-links a {
    font-size: 0.9rem;
  }
}
@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.profession-selector-section {
  padding: 8rem 2rem;
  background: var(--gradient-dark);
  position: relative;
  overflow: hidden;
}
.profession-selector-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
  animation: professionGlow 8s ease-in-out infinite alternate;
}

@keyframes professionGlow {
  0% {
    opacity: 0.3;
    transform: scale(1);
  }
  100% {
    opacity: 0.6;
    transform: scale(1.1);
  }
}
.profession-container {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.profession-scroll-container {
  margin: 8rem 0;
  position: relative;
  overflow: hidden;
}
.profession-scroll-container::before, .profession-scroll-container::after {
  content: "";
  position: absolute;
  top: 0;
  width: 40px;
  height: 100%;
  z-index: 10;
  pointer-events: none;
}
.profession-scroll-container::before {
  left: 0;
  background: linear-gradient(to right, var(--dark-bg), transparent);
}
.profession-scroll-container::after {
  right: 0;
  background: linear-gradient(to left, var(--dark-bg), transparent);
}

.profession-cards {
  display: flex;
  gap: 1rem;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  padding: 2rem 1rem;
  scrollbar-width: none;
  -ms-overflow-style: none;
  user-select: none;
}
.profession-cards::-webkit-scrollbar {
  display: none;
}
.profession-cards {
  -webkit-overflow-scrolling: touch;
  scroll-snap-type: x mandatory;
}

.profession-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 0.5rem 1rem;
  min-width: fit-content;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.05);
}
.profession-card:hover {
  transform: translateY(-2px);
  border-color: rgba(0, 102, 255, 0.4);
  box-shadow: 0 5px 15px rgba(0, 102, 255, 0.15);
  background: rgba(255, 255, 255, 0.08);
}
.profession-card.active {
  border-color: var(--primary-color);
  background: rgba(0, 102, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 102, 255, 0.2);
}
.profession-card.active .profession-icon {
  transform: scale(1.1);
}
.profession-card.active .profession-title {
  color: var(--primary-color);
}

.profession-icon {
  font-size: 1.2rem;
  transition: transform 0.3s ease;
  flex-shrink: 0;
}

.profession-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-primary);
  line-height: 1.1;
  white-space: nowrap;
  flex-shrink: 0;
}

.profession-content {
  margin-top: 8rem;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.8s ease-out;
}
.profession-content.visible {
  opacity: 1;
  transform: translateY(0);
}

.content-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  padding: 8rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
}
.content-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 20%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.content-header {
  margin-bottom: 4rem;
  position: relative;
  z-index: 2;
}

.agent-label {
  font-size: 0.85rem;
  color: var(--text-secondary);
  opacity: 0.8;
  margin-bottom: 0.5rem;
  font-weight: 500;
  transition: all 0.4s ease-in-out;
}
.agent-label.text-rotating {
  opacity: 0;
}

.content-header h3 {
  font-size: 2rem;
  font-weight: 900;
  color: var(--text-primary);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
}

.content-description {
  position: relative;
  z-index: 2;
}
.content-description p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 4rem;
}

.content-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 8rem;
  position: relative;
  z-index: 2;
}

.feature-item {
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  color: var(--text-primary);
  font-weight: 500;
  transition: all 0.3s ease;
}
.feature-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateX(5px);
}

.content-cta {
  text-align: center;
  position: relative;
  z-index: 2;
}

.profession-cta-btn {
  background: var(--gradient-primary);
  color: white;
  padding: 2rem 8rem;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  display: inline-block;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.profession-cta-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 102, 255, 0.4);
}
.profession-cta-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.profession-cta-btn:hover::before {
  left: 100%;
}
.profession-cta-btn {
  position: relative;
  overflow: hidden;
}
.profession-cta-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.profession-cta-btn:hover::before {
  left: 100%;
}

@media (max-width: 767px) {
  .profession-selector-section {
    padding: 4rem 1rem;
  }
  .profession-scroll-container::before, .profession-scroll-container::after {
    width: 30px;
  }
  .profession-cards {
    padding: 1rem;
    gap: 1rem;
  }
  .profession-card {
    min-width: 160px;
    padding: 2rem;
  }
  .profession-icon {
    font-size: 2.5rem;
  }
  .profession-title {
    font-size: 1rem;
  }
  .content-card {
    padding: 2rem;
  }
  .content-header {
    flex-direction: column;
    text-align: center;
  }
  .content-header h3 {
    font-size: 1.5rem;
    margin-top: 1rem;
  }
  .content-icon {
    width: 60px;
    height: 60px;
    font-size: 2rem;
    margin-right: 0;
  }
  .content-features {
    grid-template-columns: 1fr;
  }
  .profession-cta-btn {
    padding: 1rem 2rem;
    font-size: 1rem;
  }
  .agent-label {
    font-size: 0.8rem;
    line-height: 1.3;
  }
}