@use '../base/variables' as v;
@use '../base/mixins' as m;

.legal-page {
    background: var(--dark-bg);
    min-height: 100vh;
    padding: v.$spacing-xl 0;
    position: relative;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 20% 20%, rgba(0, 102, 255, 0.05) 0%, transparent 50%);
        pointer-events: none;
    }
}

.legal-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 v.$spacing-md;
    position: relative;
    z-index: 2;
}

// Header Section
.legal-header {
    text-align: center;
    margin-bottom: v.$spacing-xl;
    padding-bottom: v.$spacing-lg;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.legal-breadcrumb {
    font-size: 0.9rem;
    color: var(--text-secondary);
    margin-bottom: v.$spacing-md;
    
    a {
        color: var(--primary-color);
        text-decoration: none;
        transition: color v.$transition-normal;
        
        &:hover {
            color: var(--secondary-color);
        }
    }
    
    span {
        margin: 0 v.$spacing-xs;
        
        &:last-child {
            color: var(--text-primary);
        }
    }
}

.legal-header h1 {
    font-size: clamp(2rem, 5vw, 3rem);
    font-weight: 900;
    color: var(--text-primary);
    margin-bottom: v.$spacing-md;
    @include m.gradient-text(var(--gradient-primary));
}

.legal-intro {
    color: var(--text-secondary);
    font-size: 1rem;
    margin: 0;
    
    time {
        color: var(--primary-color);
        font-weight: 600;
    }
}

// Table of Contents
.legal-toc {
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    padding: v.$spacing-lg;
    margin-bottom: v.$spacing-xl;
    border: 1px solid rgba(255, 255, 255, 0.1);
    
    h3 {
        color: var(--text-primary);
        font-size: 1.2rem;
        font-weight: 700;
        margin-bottom: v.$spacing-md;
        display: flex;
        align-items: center;
        gap: v.$spacing-xs;
        
        &::before {
            content: 'ðŸ“‹';
            font-size: 1rem;
        }
    }
    
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        
        li {
            margin-bottom: v.$spacing-sm;
            
            a {
                color: var(--text-secondary);
                text-decoration: none;
                font-size: 0.95rem;
                display: flex;
                align-items: center;
                padding: v.$spacing-xs v.$spacing-sm;
                border-radius: v.$radius-sm;
                transition: all v.$transition-normal;
                
                &:hover {
                    color: var(--text-primary);
                    background: rgba(255, 255, 255, 0.05);
                    transform: translateX(5px);
                }
                
                &::before {
                    content: 'â†’';
                    margin-right: v.$spacing-xs;
                    color: var(--primary-color);
                    font-weight: bold;
                }
            }
        }
    }
}

// Content Section
.legal-content {
    line-height: 1.8;
    
    section {
        margin-bottom: v.$spacing-xl;
        scroll-margin-top: 100px; // Para navegaciÃ³n con anclas
        
        h2 {
            font-size: 1.8rem;
            font-weight: 700;
            color: var(--text-primary);
            margin-bottom: v.$spacing-lg;
            padding-bottom: v.$spacing-sm;
            border-bottom: 2px solid var(--primary-color);
            position: relative;
            
            &::before {
                content: '';
                position: absolute;
                left: 0;
                bottom: -2px;
                width: 60px;
                height: 2px;
                background: var(--gradient-primary);
            }
        }
        
        h3 {
            font-size: 1.3rem;
            font-weight: 600;
            color: var(--text-primary);
            margin: v.$spacing-lg 0 v.$spacing-md;
            
            &::before {
                content: 'â–¸';
                color: var(--primary-color);
                margin-right: v.$spacing-xs;
                font-size: 0.9em;
            }
        }
        
        h4 {
            font-size: 1.1rem;
            font-weight: 600;
            color: var(--text-primary);
            margin: v.$spacing-md 0 v.$spacing-sm;
        }
        
        p {
            color: var(--text-secondary);
            margin-bottom: v.$spacing-md;
            
            strong {
                color: var(--text-primary);
                font-weight: 600;
            }
        }
        
        ul, ol {
            margin: v.$spacing-md 0;
            padding-left: v.$spacing-lg;
            
            li {
                color: var(--text-secondary);
                margin-bottom: v.$spacing-xs;
                
                strong {
                    color: var(--text-primary);
                }
            }
        }
        
        ul li {
            position: relative;
            
            &::marker {
                color: var(--primary-color);
            }
        }
        
        a {
            color: var(--primary-color);
            text-decoration: none;
            border-bottom: 1px solid transparent;
            transition: all v.$transition-normal;
            
            &:hover {
                color: var(--secondary-color);
                border-bottom-color: var(--secondary-color);
            }
        }
    }
}

// Special Elements
.privacy-highlight {
    @include m.glassmorphism(0.1);
    border: 1px solid rgba(0, 255, 136, 0.3);
    border-radius: v.$radius-md;
    padding: v.$spacing-md;
    margin: v.$spacing-lg 0;
    background: rgba(0, 255, 136, 0.05);
    
    p {
        margin: 0;
        color: var(--text-primary);
        
        strong {
            color: var(--success-color);
        }
    }
}

.cookie-types {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: v.$spacing-md;
    margin: v.$spacing-lg 0;
}

.cookie-type {
    @include m.glassmorphism();
    border-radius: v.$radius-sm;
    padding: v.$spacing-md;
    border: 1px solid rgba(255, 255, 255, 0.1);
    
    h4 {
        color: var(--primary-color);
        margin-bottom: v.$spacing-sm;
        font-size: 1rem;
    }
    
    p {
        margin: 0;
        font-size: 0.9rem;
        color: var(--text-secondary);
    }
}

.rights-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: v.$spacing-md;
    margin: v.$spacing-lg 0;
}

.right-item {
    @include m.glassmorphism();
    border-radius: v.$radius-sm;
    padding: v.$spacing-md;
    border: 1px solid rgba(255, 255, 255, 0.1);
    text-align: center;
    transition: all v.$transition-normal;
    
    &:hover {
        background: rgba(255, 255, 255, 0.08);
        transform: translateY(-2px);
    }
    
    h4 {
        color: var(--primary-color);
        margin-bottom: v.$spacing-sm;
        font-size: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: v.$spacing-xs;
    }
    
    p {
        margin: 0;
        font-size: 0.9rem;
        color: var(--text-secondary);
    }
}

.retention-table {
    @include m.glassmorphism();
    border-radius: v.$radius-sm;
    overflow: hidden;
    margin: v.$spacing-lg 0;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.retention-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    
    &:not(:last-child) {
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    }
    
    .retention-type,
    .retention-period {
        padding: v.$spacing-md;
    }
    
    .retention-type {
        font-weight: 600;
        color: var(--text-primary);
        background: rgba(255, 255, 255, 0.02);
    }
    
    .retention-period {
        color: var(--text-secondary);
        border-left: 1px solid rgba(255, 255, 255, 0.1);
    }
}

.contact-infoL {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: v.$spacing-md;
    margin: v.$spacing-lg 0;
    
    .contact-method {
        @include m.glassmorphism();
        border-radius: v.$radius-sm;
        padding: v.$spacing-md;
        border: 1px solid rgba(255, 255, 255, 0.1);
        
        h4 {
            color: var(--primary-color);
            margin-bottom: v.$spacing-sm;
            font-size: 1rem;
            display: flex;
            align-items: center;
            gap: v.$spacing-xs;
        }
        
        p {
            margin: 0;
            font-size: 0.9rem;
            
            strong {
                display: block;
                margin-bottom: v.$spacing-xs;
            }
        }
    }
}

.privacy-commitment {
    @include m.glassmorphism(0.1);
    border: 1px solid rgba(0, 102, 255, 0.3);
    border-radius: v.$radius-md;
    padding: v.$spacing-lg;
    margin-top: v.$spacing-lg;
    background: rgba(0, 102, 255, 0.05);
    text-align: center;
    
    h4 {
        color: var(--primary-color);
        margin-bottom: v.$spacing-sm;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: v.$spacing-xs;
    }
    
    p {
        margin: 0;
        color: var(--text-primary);
        font-style: italic;
    }
}

// Footer Navigation
.legal-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: v.$spacing-xl;
    padding-top: v.$spacing-lg;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    
    @include m.mobile {
        flex-direction: column;
        gap: v.$spacing-md;
        text-align: center;
    }
}

.legal-nav-link {
    color: var(--primary-color);
    text-decoration: none;
    font-weight: 600;
    padding: v.$spacing-sm v.$spacing-md;
    border-radius: v.$radius-sm;
    transition: all v.$transition-normal;
    display: inline-flex;
    align-items: center;
    gap: v.$spacing-xs;
    
    &:hover {
        background: rgba(0, 102, 255, 0.1);
        color: var(--secondary-color);
        transform: translateY(-2px);
    }
}

// Responsive Design
@include m.mobile {
    .legal-page {
        padding: v.$spacing-lg 0;
    }
    
    .legal-container {
        padding: 0 v.$spacing-sm;
    }
    
    .legal-toc {
        padding: v.$spacing-md;
    }
    
    .legal-content {
        section {
            h2 {
                font-size: 1.5rem;
            }
            
            h3 {
                font-size: 1.2rem;
            }
        }
    }
    
    .cookie-types,
    .rights-grid {
        grid-template-columns: 1fr;
    }
    
    .retention-row {
        grid-template-columns: 1fr;
        
        .retention-period {
            border-left: none;
            border-top: 1px solid rgba(255, 255, 255, 0.1);
        }
    }
    
    .contact-info {
        grid-template-columns: 1fr;
    }
}

// Smooth scrolling for anchor links
html {
    scroll-behavior: smooth;
}

// Highlight target section when navigating via anchors
.legal-content section:target {
    background: rgba(0, 102, 255, 0.05);
    border-radius: v.$radius-sm;
    padding: v.$spacing-md;
    margin: 0 -#{v.$spacing-md} v.$spacing-xl -#{v.$spacing-md};
    animation: highlightSection 2s ease-out;
    
    @include m.mobile {
        margin: 0 -#{v.$spacing-sm} v.$spacing-xl -#{v.$spacing-sm};
    }
}

@keyframes highlightSection {
    0% {
        background: rgba(0, 102, 255, 0.2);
        transform: scale(1.01);
    }
    100% {
        background: rgba(0, 102, 255, 0.05);
        transform: scale(1);
    }
}