@use '../base/variables' as v;
@use '../base/mixins' as m;

// Blog Hero
.blog-hero {
    min-height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--gradient-dark);
    text-align: center;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 30% 40%, rgba(0, 102, 255, 0.15) 0%, transparent 50%);
        animation: heroGlow 8s ease-in-out infinite alternate;
    }

    .hero-content {
        max-width: 800px;
        z-index: 2;
        position: relative;
        padding: v.$spacing-md;

        h1 {
            font-size: clamp(2.5rem, 6vw, 4rem);
            font-weight: 900;
            margin-bottom: v.$spacing-md;
            @include m.gradient-text(var(--gradient-primary));
        }
    }
}

// Blog Container
.blog-container {
    max-width: 1200px;
    margin: 0 auto;
}

// Blog Grid
.blog-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: v.$spacing-lg;
}

.blog-card {
    @include m.glassmorphism();
    border-radius: v.$radius-lg;
    overflow: hidden;
    @include m.card-hover();

    .blog-image {
        height: 200px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform v.$transition-slow;
        }
    }

    &:hover .blog-image img {
        transform: scale(1.05);
    }

    .blog-content {
        padding: v.$spacing-md;
    }

    .blog-meta {
        font-size: 0.9rem;
        color: var(--text-secondary);
        margin-bottom: v.$spacing-sm;
    }

    .blog-title a {
        color: var(--text-primary);
        text-decoration: none;
        font-size: 1.3rem;
        font-weight: 700;
        line-height: 1.4;
        transition: color v.$transition-normal;

        &:hover {
            @include m.gradient-text(var(--gradient-primary));
        }
    }

    .blog-excerpt {
        color: var(--text-secondary);
        margin: v.$spacing-sm 0;
        line-height: 1.6;
    }

    .read-more {
        color: var(--primary-color);
        text-decoration: none;
        font-weight: 600;
        display: inline-flex;
        align-items: center;
        gap: v.$spacing-xs;
        transition: all v.$transition-normal;

        &:hover {
            transform: translateX(5px);
        }

        span {
            transition: transform v.$transition-normal;
        }

        &:hover span {
            transform: translateX(3px);
        }
    }
}

// PASO 6 CORREGIDO: Blog Post con mismo diseÃ±o que el visual-editor
.blog-post {
    max-width: 75%; // Mismo max-width que el editor
    margin: 0 auto;
    padding: v.$spacing-lg; // Mismo padding que el editor
    
    // MISMO BACKGROUND QUE TENÃA POST-CONTENT ANTES
    background: rgba(255, 255, 255, 0.07);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: v.$radius-lg;
    margin-top: 12rem;
    margin-bottom: v.$spacing-lg;
}

.post-header {
    text-align: center;
    margin-bottom: v.$spacing-xl;

    .post-meta {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: v.$spacing-md;
        font-size: 0.9rem;
        color: var(--text-secondary);
    }

    .back-blog {
        color: var(--primary-color);
        text-decoration: none;
        transition: color v.$transition-normal;
        border: solid 1px var(--primary-color);
        padding: .5rem 1rem;

        &:hover {
            color: var(--secondary-color);
            border-color: var(--secondary-color);
        }
    }

    .post-title {
        font-size: clamp(2rem, 5vw, 3.5rem);
        font-weight: 900;
        line-height: 1.2;
        margin-bottom: v.$spacing-md;
        color: var(--text-primary);
    }

    .post-excerpt {
        font-size: 1.2rem;
        color: var(--text-secondary);
        line-height: 1.6;
    }
}

.post-image {
    margin: v.$spacing-xl 0;
    border-radius: v.$radius-lg;
    overflow: hidden;

    img {
        width: 75%;
        margin: auto;
        height: auto;
        display: block;
    }
}

// Post Content - SIN ningÃºn background, completamente transparente
.post-content {
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--text-primary);
    // SIN background, border, padding adicional - solo hereda del contenedor padre
    margin-bottom: v.$spacing-lg;

    p {
        margin-bottom: v.$spacing-md;
    }



    a {
        color: var(--primary-color);
        transition: color v.$transition-normal;

        &:hover {
            color: var(--secondary-color);
        }
    }

    // Colores de texto (deben funcionar en el post final)
    .text-primary, span.color-primary { color: var(--primary-color) !important; }
    .text-secondary, span.color-secondary { color: var(--secondary-color) !important; }
    .text-accent, span.color-accent { color: var(--accent-color) !important; }
    .text-success, span.color-success { color: var(--success-color) !important; }
    .text-warning, span.color-warning { color: var(--warning-color) !important; }
    .text-danger, span.color-danger { color: var(--danger-color) !important; }
    
    // TamaÃ±os de texto (deben funcionar en el post final)
    span.font-small { font-size: 0.9rem !important; }
    span.font-normal { font-size: 1.2rem !important; }
    span.font-large { font-size: 1.5rem !important; }
    span.font-xl { font-size: 1.8rem !important; }

    .text-align-left { 
        text-align: left !important; 
    }
    .text-align-center { 
        text-align: center !important; 
    }
    .text-align-right { 
        text-align: right !important; 
    }

    .content-row{
        margin: 1.5rem 0;
        align-items: center;
    }
}

// PASO 8: Post Footer con botones compartir corregidos
.post-footer {
    margin-top: v.$spacing-xl;
    padding-top: v.$spacing-lg;
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    .post-actions {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: v.$spacing-md;
        flex-wrap: wrap;
    }

    .share-buttons {
        display: flex;
        align-items: center;
        gap: v.$spacing-sm;
        flex-wrap: wrap;

        span {
            color: var(--text-secondary);
            font-weight: 600;
            margin-right: v.$spacing-xs;
            white-space: nowrap;
        }

        .share-btn {
            background: rgba(255, 255, 255, 0.1);
            color: var(--text-primary);
            padding: v.$spacing-xs v.$spacing-sm;
            border-radius: v.$radius-sm;
            text-decoration: none;
            font-size: 0.9rem;
            transition: all v.$transition-normal;
            border: 1px solid rgba(255, 255, 255, 0.2);
            white-space: nowrap;
            min-width: 80px;
            text-align: center;

            &:hover {
                background: var(--primary-color);
                border-color: var(--primary-color);
                transform: translateY(-2px);
            }

            &.whatsapp:hover {
                background: #25D366;
                border-color: #25D366;
            }

            &.facebook:hover {
                background: #1877F2;
                border-color: #1877F2;
            }

            &.x:hover {
                background: #000000;
                border-color: #000000;
            }
        }
    }
}

// Related CTA
.related-cta {
    text-align: center;
    margin-bottom: v.$spacing-xl;

    .cta-content {
        max-width: 600px;
        margin: 0 auto;

        h2 {
            font-size: 2rem;
            margin-bottom: v.$spacing-md;
            color: var(--text-primary);
        }

        p {
            color: var(--text-secondary);
            font-size: 1.1rem;
            margin-bottom: v.$spacing-lg;
        }

        .hero-buttons {
            display: flex;
            gap: v.$spacing-md;
            justify-content: center;
            flex-wrap: wrap;
        }
    }
}

// RESPONSIVE PARA MÃ“VIL
@include m.mobile {
    .blog-grid {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .blog-post {
        min-width: 100%;
        padding: v.$spacing-lg v.$spacing-xs; // Reducir padding en mÃ³vil pero mantener diseÃ±o
        margin-top: v.$spacing-sm;
        margin-bottom: v.$spacing-sm;
    }

    .post-header {
        .post-meta {
            flex-direction: column;
            gap: v.$spacing-xs;
            text-align: center;
        }
    }

    .post-content {
        font-size: 1.2rem;
    }

    // PASO 8: Responsive especÃ­fico para botones compartir en mÃ³vil
    .post-footer {
        .post-actions {
            flex-direction: column;
            align-items: stretch;
            gap: v.$spacing-md;
            
            // BotÃ³n "Volver" ocupa todo el ancho en mÃ³vil
            .btn-secondary {
                width: 100%;
                text-align: center;
                padding: v.$spacing-sm;
            }
        }

        .share-buttons {
            justify-content: center;
            width: 100%;
            padding: 0 v.$spacing-sm;
            
            // El texto "Compartir:" se muestra arriba
            span {
                width: 100%;
                text-align: center;
                margin-bottom: v.$spacing-xs;
                margin-right: 0;
            }
            
            // Los botones se organizan en una fila que cabe en mÃ³vil
            .share-btn {
                flex: 1;
                max-width: 90px;
                font-size: 0.8rem;
                padding: v.$spacing-xs;
                margin: 0 2px;
            }
        }
    }

    .related-cta {
        padding: v.$spacing-lg;

        .cta-content {
            h2 {
                font-size: 1.5rem;
            }

            .hero-buttons {
                flex-direction: column;

                .btn-primary,
                .btn-secondary {
                    width: 100%;
                }
            }
        }
    }
}

// Para pantallas muy pequeÃ±as (< 400px)
@media (max-width: 400px) {
    .post-footer {
        .share-buttons {
            flex-direction: column;
            gap: v.$spacing-xs;
            
            .share-btn {
                width: 100%;
                max-width: none;
                flex: none;
                font-size: 0.9rem;
                padding: v.$spacing-sm;
                margin: 0;
            }
        }
    }
}

// Animaciones
@keyframes heroGlow {
    0% {
        opacity: 0.5;
        transform: scale(1);
    }
    100% {
        opacity: 0.8;
        transform: scale(1.1);
    }
}