@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact-hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: linear-gradient(135deg, #0a0a0f, #1a1a2e, #2d1b69);
    overflow: hidden;
}

.hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 
        radial-gradient(circle at 30% 40%, rgba(37, 211, 102, 0.15) 0%, transparent 50%),
        radial-gradient(circle at 70% 60%, rgba(24, 119, 242, 0.15) 0%, transparent 50%),
        radial-gradient(circle at 50% 80%, rgba(228, 64, 95, 0.15) 0%, transparent 50%);
    animation: heroFlow 8s ease-in-out infinite alternate;
}

@keyframes heroFlow {
    0% { 
        transform: scale(1) rotate(0deg); 
        opacity: 0.6; 
    }
    100% { 
        transform: scale(1.1) rotate(5deg); 
        opacity: 0.9; 
    }
}

.hero-emoji {
    font-size: 8rem;
    margin: v.$spacing-lg auto;
    animation: bounce 2s ease-in-out infinite;
    display: block;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
    40% { transform: translateY(-20px); }
    60% { transform: translateY(-10px); }
}

.hero-title {
    font-size: clamp(3rem, 7vw, 6rem);
    font-weight: 900;
    margin-bottom: v.$spacing-md;
    @include m.gradient-text(var(--gradient-primary));
    animation: titleGlow 3s ease-in-out infinite alternate;
}

@keyframes titleGlow {
    0% { filter: drop-shadow(0 0 10px rgba(0, 102, 255, 0.3)); }
    100% { filter: drop-shadow(0 0 30px rgba(0, 102, 255, 0.8)); }
}

.choice-section {
    padding: v.$spacing-xl v.$spacing-md;
    max-width: v.$desktop;
    margin: 0 auto;
    position: relative;
}

.choice-cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: v.$spacing-lg;
    margin-top: v.$spacing-lg;
}

.choice-card {
    @include m.glassmorphism();
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 30px;
    padding: v.$spacing-lg;
    text-align: center;
    position: relative;
    overflow: hidden;
    @include m.card-hover(1.02);
    cursor: pointer;
    @include m.rotating-border();

    &.instant {
        border-color: rgba(37, 211, 102, 0.4);

        &:hover {
            border-color: rgba(37, 211, 102, 0.8);
            box-shadow: 0 20px 60px rgba(37, 211, 102, 0.3);
        }
    }

    &.traditional {
        border-color: rgba(255, 170, 0, 0.4);

        &:hover {
            border-color: rgba(255, 170, 0, 0.8);
            box-shadow: 0 20px 60px rgba(255, 170, 0, 0.3);
        }
    }
}

.card-badge {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: v.$spacing-xs 1.5rem;
    border-radius: 25px;
    font-size: 0.9rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.instant .card-badge {
    background: var(--gradient-success);
    color: white;
    animation: badgePulse 2s ease-in-out infinite;
}

.traditional .card-badge {
    background: var(--gradient-accent);
    color: white;
}

@keyframes badgePulse {
    0%, 100% { transform: translateX(-50%) scale(1); }
    50% { transform: translateX(-50%) scale(1.1); }
}

.card-icon {
    width: 100px;
    height: 100px;
    margin: 0 auto v.$spacing-md;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    position: relative;
    transition: all v.$transition-normal;
}

.instant .card-icon {
    background: var(--gradient-success);
    animation: instantPulse 2s ease-in-out infinite;
}

.traditional .card-icon {
    background: var(--gradient-accent);
}

@keyframes instantPulse {
    0%, 100% { 
        transform: scale(1); 
        box-shadow: 0 0 20px rgba(37, 211, 102, 0.3); 
    }
    50% { 
        transform: scale(1.1); 
        box-shadow: 0 0 40px rgba(37, 211, 102, 0.6); 
    }
}

.choice-card:hover .card-icon {
    transform: rotateY(360deg);
}

.card-title {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: v.$spacing-sm;
}

.card-description {
    color: var(--text-secondary);
    margin-bottom: v.$spacing-md;
    line-height: 1.6;
}

.bot-channels {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin: v.$spacing-md 0;
}

.bot-channel {
    @include m.glassmorphism();
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    padding: 1.5rem;
    text-align: center;
    transition: all v.$transition-normal;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    text-decoration: none;
    color: inherit;
    height: 200.3px;
    align-content: center;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
        transition: left 0.5s;
    }

    &:hover::before {
        left: 100%;
    }

    &.whatsapp:hover {
        border-color: var(--whatsapp-color);
        box-shadow: 0 10px 30px rgba(37, 211, 102, 0.3);
        transform: translateY(-5px);
    }

    &.facebook:hover {
        border-color: var(--facebook-color);
        box-shadow: 0 10px 30px rgba(24, 119, 242, 0.3);
        transform: translateY(-5px);
    }

    &.instagram:hover {
        border-color: var(--instagram-color);
        box-shadow: 0 10px 30px rgba(228, 64, 95, 0.3);
        transform: translateY(-5px);
    }
}

// Actualizar solo la secciÃ³n .channel-icon para imÃ¡genes

.channel-icon {
    width: 50px;
    height: 50px;
    margin: 0 auto v.$spacing-sm;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    transition: all v.$transition-normal;
    overflow: hidden; // Para contener la imagen
    
    // Estilos para las imÃ¡genes
    img {
        width: 32px;
        height: 32px;
        object-fit: contain;
        transition: transform v.$transition-normal;
        filter: brightness(1.2) contrast(1.1); // Mejor visibilidad sobre el fondo
    }
}

.whatsapp .channel-icon {
    background: var(--gradient-whatsapp);
}

.facebook .channel-icon {
    background: var(--gradient-facebook);
}

.instagram .channel-icon {
    background: var(--gradient-instagram);
}

.bot-channel:hover .channel-icon {
    transform: rotateY(180deg) scale(1.1);
    
    img {
        transform: rotateY(-180deg) scale(1.1); // Contrarresta la rotaciÃ³n del contenedor
    }
}

.whatsapp .channel-icon {
    background: var(--gradient-whatsapp);
}

.facebook .channel-icon {
    background: var(--gradient-facebook);
}

.instagram .channel-icon {
    background: var(--gradient-instagram);
}

.bot-channel:hover .channel-icon {
    transform: rotateY(180deg) scale(1.1);
}

.channel-label {
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--text-secondary);
}

.traditional-methods {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    margin: v.$spacing-md 0;
}

.traditional-method {
    @include m.glassmorphism();
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    padding: v.$spacing-md;
    text-align: center;
    transition: all v.$transition-normal;
    cursor: pointer;
    text-decoration: none;
    color: inherit;

    &:hover {
        background: rgba(255, 255, 255, 0.08);
        border-color: rgba(255, 170, 0, 0.4);
        transform: translateY(-5px);
        box-shadow: 0 15px 40px rgba(255, 170, 0, 0.2);
    }
}

.method-icon {
    width: 60px;
    height: 60px;
    margin: 0 auto v.$spacing-sm;
    background: var(--gradient-accent);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8rem;
    transition: all v.$transition-normal;
}

.traditional-method:hover .method-icon {
    transform: rotateX(360deg);
}

.method-label {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: v.$spacing-xs;
}

.method-detail {
    color: var(--text-secondary);
    font-size: 0.9rem;
}

.warning-note {
    background: rgba(255, 170, 0, 0.1);
    border: 1px solid rgba(255, 170, 0, 0.3);
    border-radius: 15px;
    padding: v.$spacing-sm;
    margin-top: 1.5rem;
    color: var(--warning-color);
    font-size: 0.9rem;
    text-align: center;
    animation: warningBlink 3s ease-in-out infinite;
}

@keyframes warningBlink {
    0%, 100% { opacity: 0.7; }
    50% { opacity: 1; }
}

@keyframes patternMove {
    0% { transform: translate(0, 0); }
    100% { transform: translate(-30px, -30px); }
}

.demo-container {
    max-width: 1000px;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

.phone-mockup {
    max-width: 480px;
    margin: 0 auto;
    background: #000;
    border-radius: 30px;
    padding: v.$spacing-sm;
    box-shadow: 0 20px 60px rgb(137 135 135 / 50%);
    position: relative;
}

.phone-screen {
    background: var(--dark-bg);
    border-radius: 25px;
    padding: v.$spacing-md v.$spacing-sm;
    min-height: 600px;
    position: relative;
    overflow: hidden;
}

.whatsapp-header {
    display: flex;
    align-items: center;
    padding: v.$spacing-xs;
    background: var(--whatsapp-color);
    border-radius: 15px 15px 0 0;
    margin: -#{v.$spacing-md} -#{v.$spacing-sm} v.$spacing-sm;
    position: relative;
    z-index: 2;
}

.contact-avatar {
    width: 40px;
    height: 40px;
    background: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: v.$spacing-sm;
    font-size: 1.8rem;
}

.contact-info h3 {
    color: white;
    font-size: 1.1rem;
    margin-bottom: 0.2rem;
}

.contact-status {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.65rem;
}

.try-button {
    background: var(--gradient-whatsapp);
    color: white;
    padding: v.$spacing-sm v.$spacing-md;
    border-radius: 25px;
    border: none;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
    transition: all v.$transition-normal;
    text-decoration: none;
    display: block;
    text-align: center;
    margin: v.$spacing-md auto v.$spacing-xl;
    position: relative;
    overflow: hidden;
    width: 90%;
    @include m.button-hover(rgba(37, 211, 102, 0.4));

    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 15px 40px rgba(37, 211, 102, 0.4);
    }
}


.chat-fab {
    width: 70px;
    height: 70px;
    background: var(--gradient-whatsapp);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
    text-decoration: none;
    box-shadow: 0 10px 30px rgba(37, 211, 102, 0.4);
    transition: all v.$transition-normal;
    @include m.pulse-animation(rgba(37, 211, 102, 0.4));

    &:hover {
        transform: scale(1.1);
        box-shadow: 0 15px 40px rgba(37, 211, 102, 0.6);
    }
}

.chat-tooltip {
    position: absolute;
    right: 80px;
    top: 50%;
    transform: translateY(-50%);
    background: white;
    color: var(--dark-bg);
    padding: 0.8rem 1.2rem;
    border-radius: 15px;
    font-weight: 600;
    white-space: nowrap;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    opacity: 0;
    pointer-events: none;
    transition: all v.$transition-normal;

    &::after {
        content: '';
        position: absolute;
        right: -8px;
        top: 50%;
        transform: translateY(-50%);
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-left-color: white;
    }
}

.chat-fab:hover .chat-tooltip {
    opacity: 1;
    transform: translateY(-50%) translateX(-10px);
}

@include m.mobile {
    .choice-cards {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
    }

    .bot-channels {
        grid-template-columns: 1fr;
        gap: v.$spacing-sm;
    }

    .traditional-methods {
        grid-template-columns: 1fr;
        gap: v.$spacing-sm;
    }

    .phone-mockup {
        max-width: 300px;
    }

    .floating-chat {
        bottom: v.$spacing-sm;
        right: v.$spacing-sm;
    }

    .chat-fab {
        width: 60px;
        height: 60px;
        font-size: 1.8rem;
    }

    .chat-tooltip {
        display: none;
    }
}