@use '../base/variables' as v;
@use '../base/mixins' as m;

// Admin Auth Page Styles
.auth-page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: v.$spacing-md;
    background: var(--dark-bg);
    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%),
                    radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
        animation: authBackground 8s ease-in-out infinite alternate;
    }
}

@keyframes authBackground {
    0% { opacity: 0.3; transform: scale(1); }
    100% { opacity: 0.6; transform: scale(1.1); }
}

.auth-container {
    @include m.glassmorphism(0.05, 20px);
    border-radius: 25px;
    padding: v.$spacing-xl;
    width: 100%;
    max-width: 800px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
    z-index: 2;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at 30% 20%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
        pointer-events: none;
    }
}

.auth-header {
    text-align: center;
    margin-bottom: v.$spacing-lg;
    position: relative;
    z-index: 2;
}

.auth-icon {
    font-size: 4rem;
    margin-bottom: v.$spacing-md;
    animation: authPulse 2s ease-in-out infinite;
}

@keyframes authPulse {
    0%, 100% { transform: scale(1); opacity: 0.8; }
    50% { transform: scale(1.1); opacity: 1; }
}

.auth-title {
    font-size: 2rem;
    font-weight: 900;
    color: var(--text-primary);
    margin-bottom: v.$spacing-xs;
    @include m.gradient-text(var(--gradient-primary));
}

.auth-subtitle {
    color: var(--text-secondary);
    font-size: 1rem;
    margin: 0;
}

.auth-form {
    position: relative;
    z-index: 2;

    .form-group {
        margin-bottom: v.$spacing-md;
    }

    .form-label {
        display: block;
        color: var(--text-secondary);
        margin-bottom: v.$spacing-xs;
        font-weight: 600;
        font-size: 0.9rem;
    }

    .form-input {
        width: 100%;
        padding: v.$spacing-md;
        border: 2px solid rgba(255, 255, 255, 0.1);
        border-radius: v.$radius-md;
        background: rgba(255, 255, 255, 0.05);
        color: var(--text-primary);
        font-size: 1rem;
        transition: all v.$transition-normal;
        box-sizing: border-box;
        font-family: inherit;

        &:focus {
            outline: none;
            border-color: var(--primary-color);
            background: rgba(255, 255, 255, 0.08);
            box-shadow: 0 0 0 3px rgba(0, 102, 255, 0.1);
        }

        &::placeholder {
            color: var(--text-secondary);
            opacity: 0.6;
        }
    }

    .auth-button {
        width: 100%;
        padding: v.$spacing-md;
        background: var(--gradient-primary);
        border: none;
        border-radius: v.$radius-md;
        color: white;
        font-size: 1.1rem;
        font-weight: 600;
        cursor: pointer;
        transition: all v.$transition-normal;
        position: relative;
        overflow: hidden;
        @include m.button-hover();

        &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
            transform: none;

            &:hover {
                transform: none;
                box-shadow: none;
            }
        }
    }

    .error-message {
        background: rgba(255, 71, 87, 0.1);
        border: 1px solid rgba(255, 71, 87, 0.3);
        color: var(--danger-color);
        padding: v.$spacing-sm;
        border-radius: v.$radius-sm;
        margin-bottom: v.$spacing-md;
        font-size: 0.9rem;
        display: none;
        animation: slideInDown 0.3s ease-out;
    }

    .loading {
        display: none;
        text-align: center;
        color: var(--text-secondary);
        margin-top: v.$spacing-md;
        font-size: 0.9rem;

        .loading-spinner {
            width: 20px;
            height: 20px;
            border: 2px solid rgba(0, 102, 255, 0.3);
            border-top: 2px solid var(--primary-color);
            border-radius: 50%;
            animation: spin 1s linear infinite;
            display: inline-block;
            margin-right: v.$spacing-xs;
        }
    }
}

@keyframes slideInDown {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.back-link {
    text-align: center;
    margin-top: v.$spacing-lg;
    position: relative;
    z-index: 2;

    a {
        color: var(--text-secondary);
        text-decoration: none;
        font-size: 0.9rem;
        transition: color v.$transition-normal;

        &:hover {
            color: var(--primary-color);
        }
    }
}

// Mobile responsiveness
@include m.mobile {
    .auth-container {
        padding: v.$spacing-lg;
        margin: v.$spacing-sm;
    }

    .auth-title {
        font-size: 1.8rem;
    }

    .auth-icon {
        font-size: 3rem;
    }
}