@use '../base/variables' as v;
@use '../base/mixins' as m;

.main-footer {
    position: relative;
    background: var(--gradient-dark);
    padding: v.$spacing-xl 0 v.$spacing-md;
    overflow: hidden;
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: linear-gradient(90deg, transparent, rgba(0, 102, 255, 0.5), transparent);
    }
}

.footer-container {
    max-width: v.$desktop;
    margin: 0 auto;
    padding: 0 v.$spacing-md;
    position: relative;
    z-index: 2;
}

.footer-content {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    gap: v.$spacing-xl;
    margin-bottom: v.$spacing-xl;
    
    @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
        gap: v.$spacing-lg;
    }
    
    @include m.mobile {
        grid-template-columns: 1fr;
        gap: v.$spacing-md;
        text-align: center;
    }
}

.footer-section {
    .footer-logo {
        display: flex;
        align-items: center;
        gap: v.$spacing-sm;
        margin-bottom: v.$spacing-md;
        
        @include m.mobile {
            justify-content: center;
            flex-direction: column;
            gap: v.$spacing-xs;
        }
        
        img {
            width: 40px;
            height: auto;
            filter: brightness(1.2);
        }
        
        .footer-tagline {
            font-size: 0.9rem;
            color: var(--text-secondary);
            font-weight: 500;
        }
    }
    
    .footer-description {
        color: var(--text-secondary);
        line-height: 1.6;
        margin-bottom: v.$spacing-md;
        font-size: 0.95rem;
    }
}

.footer-title {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: v.$spacing-md;
    position: relative;
    
    &::after {
        content: '';
        position: absolute;
        bottom: -4px;
        left: 0;
        width: 30px;
        height: 2px;
        background: var(--gradient-primary);
        border-radius: 1px;
        
        @include m.mobile {
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

.footer-links {
    list-style: none;
    padding: 0;
    margin: 0;
    
    li {
        margin-bottom: v.$spacing-sm;
        
        a {
            color: var(--text-secondary);
            text-decoration: none;
            font-size: 0.9rem;
            transition: all v.$transition-normal;
            position: relative;
            
            &::before {
                content: '';
                position: absolute;
                left: -15px;
                top: 50%;
                transform: translateY(-50%);
                width: 0;
                height: 2px;
                background: var(--primary-color);
                transition: width v.$transition-normal;
                
                @include m.mobile {
                    display: none;
                }
            }
            
            &:hover {
                color: var(--text-primary);
                padding-left: 20px;
                
                @include m.mobile {
                    padding-left: 0;
                    transform: translateY(-2px);
                }
                
                &::before {
                    width: 10px;
                }
            }
        }
    }
}

.social-links {
    display: flex;
    gap: v.$spacing-sm;
    margin-top: v.$spacing-md;
    
    @include m.mobile {
        justify-content: center;
    }
}

// Actualizar solo la secciÃ³n .social-link para imÃ¡genes

.social-link {
    width: 45px;
    height: 45px;
    @include m.glassmorphism(0.1);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    transition: all v.$transition-normal;
    position: relative;
    overflow: hidden;
    
    // Reemplazar estilos de span por img
    img {
        width: 24px;
        height: 24px;
        object-fit: contain;
        transition: transform v.$transition-normal;
        filter: brightness(1.3) contrast(1.2); // Mejor visibilidad sobre el fondo oscuro
    }
    
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        opacity: 0;
        transition: opacity v.$transition-normal;
        z-index: -1;
    }
    
    &:hover {
        transform: translateY(-3px);
        border-color: currentColor;
        
        img {
            transform: scale(1.1);
        }
        
        &::before {
            opacity: 0.2;
        }
    }
    
    &.whatsapp {
        &:hover {
            border-color: var(--whatsapp-color);
            box-shadow: 0 8px 25px rgba(37, 211, 102, 0.3);
        }
        
        &::before {
            background: var(--whatsapp-color);
        }
    }
    
    &.facebook {
        &:hover {
            border-color: var(--facebook-color);
            box-shadow: 0 8px 25px rgba(24, 119, 242, 0.3);
        }
        
        &::before {
            background: var(--facebook-color);
        }
    }
    
    &.instagram {
        &:hover {
            border-color: var(--instagram-color);
            box-shadow: 0 8px 25px rgba(228, 64, 95, 0.3);
        }
        
        &::before {
            background: var(--instagram-color);
        }
    }
}

.footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: v.$spacing-md;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    
    @include m.mobile {
        flex-direction: column;
        gap: v.$spacing-sm;
        text-align: center;
    }
    
    p {
        color: var(--text-secondary);
        font-size: 0.85rem;
        margin: 0;
    }
}

.footer-copyright {
    #currentYear {
        font-weight: 600;
        color: var(--primary-color);
    }
}

.footer-credit {
    p {
        font-style: italic;
        opacity: 0.8;
    }
}

// Background Elements
.footer-bg-elements {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
}

.footer-glow {
    position: absolute;
    top: -50%;
    left: 20%;
    width: 60%;
    height: 200%;
    background: radial-gradient(ellipse at center, rgba(0, 102, 255, 0.08) 0%, transparent 70%);
    animation: footerGlow 8s ease-in-out infinite alternate;
}

@keyframes footerGlow {
    0% { 
        transform: translateX(-10px) scale(1);
        opacity: 0.5;
    }
    100% { 
        transform: translateX(10px) scale(1.1);
        opacity: 0.8;
    }
}

.footer-pattern {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
        radial-gradient(circle at 25% 25%, rgba(255, 255, 255, 0.02) 1px, transparent 1px),
        radial-gradient(circle at 75% 75%, rgba(0, 102, 255, 0.02) 1px, transparent 1px);
    background-size: 50px 50px;
    opacity: 0.5;
    animation: patternFloat 20s linear infinite;
}

@keyframes patternFloat {
    0% { transform: translate(0, 0); }
    100% { transform: translate(-50px, -50px); }
}

// Animation for footer sections
.footer-section {
    opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 0.8s ease-out forwards;
    
    &:nth-child(1) { animation-delay: 0.1s; }
    &:nth-child(2) { animation-delay: 0.2s; }
    &:nth-child(3) { animation-delay: 0.3s; }
    &:nth-child(4) { animation-delay: 0.4s; }
}

@keyframes fadeInUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}