@use '../base/variables' as v;
@use '../base/mixins' as m;

// Admin Styles
.admin-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: v.$spacing-md;
    padding-bottom: v.$spacing-md;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    .admin-header-left {
        display: flex;
        flex-direction: column;
        gap: v.$spacing-xs;

        h1 {
            font-size: 2rem;
            color: var(--text-primary);
            margin: 0;
        }
    }

    .admin-user-info {
        .user-greeting {
            color: var(--text-secondary);
            font-size: 0.9rem;
            font-weight: 500;
        }
    }

    .admin-actions {
        display: flex;
        gap: v.$spacing-sm;
        align-items: center;
    }
}

// Logout Button
.btn-logout {
    background: hsla(0, 0%, 100%, .05);
    border: 3.5px solid rgb(142, 142, 142);
    padding: .2rem  .8rem;
    border-radius: 1.5rem;
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    gap: v.$spacing-xs;
    transition: all v.$transition-normal;
    cursor: pointer;

    .logout-icon {
        font-size: 2rem;
    }

    &:hover {
        background: rgba(255, 71, 87, 0.2);
        border-color: rgba(255, 71, 87, 0.5);
        transform: translateY(-2px);
        box-shadow: 0 5px 15px rgba(255, 71, 87, 0.2);
    }

    &:active {
        transform: translateY(0);
    }
}

.admin-section {
    margin: v.$spacing-md v.$spacing-md v.$spacing-xl;

    h2 {
        color: var(--text-primary);
        margin-bottom: v.$spacing-md;
        font-size: 1.5rem;
    }
}

.admin-table {
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    overflow: hidden;

    table {
        width: 100%;
        border-collapse: collapse;

        th, td {
            padding: v.$spacing-sm v.$spacing-md;
            text-align: center;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }

        th {
            background: rgba(255, 255, 255, 0.05);
            font-weight: 600;
            color: var(--text-primary);
        }

        td {
            color: var(--text-secondary);
        }

        .actions {
            gap: v.$spacing-xs;
        }
    }
}

.draft-row {
    background: rgba(255, 170, 0, 0.1);
}

.draft-badge {
    background: var(--warning-color);
    color: var(--dark-bg);
    padding: 2px 8px;
    border-radius: 12px;
    font-size: 0.8rem;
    font-weight: 600;
    margin-left: v.$spacing-xs;
}

.btn-view, .btn-edit, .btn-delete {
    width: 6rem;
    height: 2rem;
    margin: .2rem 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 4px 12px;
    border: none;
    border-radius: v.$radius-sm;
    font-size: 0.85rem;
    cursor: pointer;
    text-decoration: none;
    font-weight: 500;
}

.btn-view {
    background: var(--primary-color);
    color: white;
}

.btn-edit {
    background: var(--warning-color);
    color: var(--dark-bg);
}

.btn-delete {
    background: var(--danger-color);
    color: white;
}

.admin-stats {
    display: flex;
    gap: v.$spacing-md;
    margin: v.$spacing-xl v.$spacing-md v.$spacing-md;
}

.stat-card {
    @include m.glassmorphism();
    padding: v.$spacing-md;
    border-radius: v.$radius-md;
    text-align: center;
    flex: 1;

    h3 {
        color: var(--text-secondary);
        font-size: 0.9rem;
        margin-bottom: v.$spacing-xs;
    }

    .stat-number {
        @include m.gradient-text(var(--gradient-primary));
        font-size: 2rem;
        font-weight: 900;
    }
}

// Empty state styles
.empty-admin {
    text-align: center;
    padding: v.$spacing-xl;
    color: var(--text-secondary);
    
    @include m.glassmorphism();
    border-radius: v.$radius-md;
    margin: v.$spacing-md 0;
}

// Form Styles
.blog-form {
    max-width: 800px;
    margin: 0 auto;
}

.form-section {
    margin-bottom: v.$spacing-xl;
    
    h3 {
        color: var(--text-primary);
        margin-bottom: v.$spacing-md;
        font-size: 1.3rem;
    }
}

.form-group {
    margin-bottom: v.$spacing-md;

    label {
        display: block;
        color: var(--text-secondary);
        margin-bottom: v.$spacing-xs;
        font-weight: 600;
    }

    input, textarea, select {
        width: 100%;
        padding: v.$spacing-sm;
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: v.$radius-sm;
        background: rgba(255, 255, 255, 0.05);
        color: var(--text-primary);
        font-family: inherit;

        &:focus {
            outline: none;
            border-color: var(--primary-color);
            box-shadow: 0 0 0 2px rgba(0, 102, 255, 0.2);
        }
    }
}

.form-row {
    display: grid;
    grid-template-columns: 1fr 200px;
    gap: v.$spacing-md;
}

.form-actions {
    display: flex;
    gap: v.$spacing-md;
    justify-content: center;
    margin-bottom: v.$spacing-lg;
}

// Responsive
@include m.mobile {
    .admin-header {
        flex-direction: column;
        align-items: flex-start;
        gap: v.$spacing-md;

        .admin-header-left {
            width: 100%;
        }

        .admin-center {
            width: 100%;
            justify-content: center;
        }

        .admin-actions {
            width: 100%;
            justify-content: center;
        }
    }

    .form-row {
        grid-template-columns: 1fr;
    }

    .admin-stats {
        flex-direction: column;
    }

    .form-actions {
        flex-direction: column;
    }
}